.ri-grid {
	position: relative;
	margin: 0 auto;
	height: auto;
	ul {
		list-style: none;
		display: block;
		width: 100%;
		margin: 0;
		padding: 0;
		zoom: 1;
		&:before {
			content: '';
			display: table;
		}
		&:after {
			content: '';
			display: table;
			clear: both;
		}
		li {
			perspective: 400px;
			margin: 0;
			padding: 0;
			float: left;
			position: relative;
			display: block;
			overflow: hidden;
			background: #000;
			a {
				display: block;
				outline: none;
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				backface-visibility: hidden;
				transform-style: preserve-3d;
				background-size: 100% 100%;
				background-position: center center;
				background-repeat: no-repeat;
				background-color: #333;
				box-sizing: content-box;
			}
		}
	}
}
.ri-grid-size-1 {
	width: 55%;
}
.ri-grid-size-2 {
	width: 100%;
}
.ri-grid-size-3 {
	width: 100%;
	margin-top: 0px;
}
.ri-shadow {
	&:after,
	&:before{
		content: '';
		position: absolute;
		z-index: -2;
		bottom: 15px;
		left: 10px;
		width: 50%;
		height: 20%;
		max-width: 300px;
		max-height: 100px;
		box-shadow: 0 15px 10px rgba(0, 0, 0, 0.7);
		transform: rotate(-3deg);
	}
	&:after{
		right: 10px;
		left: auto;
		transform: rotate(3deg);
	}
}

.ri-grid-loading {
	&:after {
		display: none;
	}
	&:before {
		display: none;
	}
	.ri-loading-image {
		position: relative;
		width: 30px;
		height: 30px;
		left: 50%;
		margin: 100px 0 0 -15px;
		display: block;
	}
}
.ri-loading-image {
	display: none;
}
