.fptOnline {

	.nice-select {
        .list {
            max-height: 350px;
            overflow-y: auto;
        }
		&.open {
			.list {
				min-width: 100%;
			}
		}
	}

    // Start custom lightgallery js
    .lg-backdrop.in {
        opacity: 0.93;
    }
    .lg-sub-html {
        font-family: Arial;
        .title-sub {
            max-height: 68px;
            margin: 5px 0 0;
            font-size: 12px;
            overflow-y: auto;
        }
    }
    .lg-thumb-outer {
        text-align: center;
    }
    .lg-outer .lg-thumb {
        position: relative;
        display: inline-block;
        padding: 10px 10px !important;
        margin: 0 auto -10px !important;
    }
    // End custom lightgallery js

    .btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show>.btn-primary.dropdown-toggle {
        color: #fff;
        background-color: #5b8d52;
        border-color: #5b8d52;
    }
    .btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show>.btn-primary.dropdown-toggle:focus {
        box-shadow: 0 0 0 0.2rem rgba(91, 141, 82, 0.5);
    }
    .btn-primary.focus, .btn-primary:focus {
        color: #fff;
        background-color: #5b8d52;
        border-color: #5b8d52;
        box-shadow: 0 0 0 0.2rem rgba(91, 141, 82, 0.5);
    }
    @media (max-width: 767px) {
        .lg-sub-html {
            font-size: 14px;
            line-height: 1.4;
            left: 0;
            padding: 10px 50px 10px 12px;
            text-align: left;
        }
        .lg-outer .lg-thumb-item {
            width: 50px;
            height: 50px;
            max-width: 50px;
            max-height: 50px;
        }
        .lg-outer .lg-toogle-thumb {
            width: 45px;
            right: 0;
        }
        
    }
}

