.fptOnline {
    .mfp-bg {
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1061 !important;
        overflow: hidden;
        position: fixed;
        background: #0b0b0b;
        opacity: 0.8;
        z-index: 1060;
    }
    .mfp-wrap {
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1062 !important;
        position: fixed;
        outline: none !important;
        -webkit-backface-visibility: hidden;
    }
    .mfp-container {
        text-align: center;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        padding: 0 6px;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
    }
    .mfp-container:before {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
    }
    .mfp-content {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0 auto;
        text-align: left;
        z-index: 1045;
    }
    .mfp-close {
        width: 36px;
        height: 36px;
        line-height: 36px;
        position: absolute;
        right: 0;
        top: 0;
        text-decoration: none;
        text-align: center;
        opacity: 0.65;
        color: #333;
        font-style: normal;
        font-size: 28px;
        font-family: Arial, Baskerville, monospace;
    }
    button.mfp-close {
        overflow: visible;
        cursor: pointer;
        background: transparent;
        border: 0;
        -webkit-appearance: none;
        display: block;
        outline: none;
        z-index: 1046;
        -ms-touch-action: manipulation;
        touch-action: manipulation;
    }
    .message_box {
        z-index: 1063 !important;
    }
}
