@-webkit-keyframes schdBannerPagePulse {
    to {
        box-shadow: 0 0 0 105px rgba(232, 76, 61, 0)
    }
}
@-moz-keyframes schdBannerPagePulse {
    to {
        box-shadow: 0 0 0 105px rgba(232, 76, 61, 0)
    }
}
@-ms-keyframes schdBannerPagePulse {
    to {
        box-shadow: 0 0 0 105px rgba(232, 76, 61, 0)
    }
}
@keyframes schdBannerPagePulse {
    to {
        box-shadow: 0 0 0 105px rgba(232, 76, 61, 0)
    }
}

@keyframes lengkeng{
    0%, 100%{-webkit-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0);}
    10%, 30%, 50%, 70%, 90%{-webkit-transform:translateX(-3px);-ms-transform:translateX(-3px);transform:translateX(-3px);}
    20%, 40%, 60%, 80%{-webkit-transform:translateX(3px);-ms-transform:translateX(3px);transform:translateX(3px);}
}
.lengkeng{
    -webkit-animation-name:lengkeng;
    animation-name:lengkeng;
    -webkit-animation-duration:1s;
    animation-duration:1s;
    -webkit-animation-fill-mode:both;
    animation-fill-mode:both;
}
.animate-radia {
    &:hover {
        &:after {
            position: absolute;
            content: "";
            opacity: .8;
            left: 50%;
            top: 50%;
            -webkit-transform: translate(-50%,-50%);
            -moz-transform: translate(-50%,-50%);
            -o-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
            height: 1px;
            width: 1px;
            z-index: 4;
            border: 1px solid transparent;
            border-radius: 1px;
            box-shadow: 0 0 0 0 rgba(255,255,255,.8);
            background-color: rgba(255,255,255,.1);
            -webkit-animation: schdBannerPagePulse 1.6s infinite cubic-bezier(.66,0,0,1);
            -moz-animation: schdBannerPagePulse 1.6s infinite cubic-bezier(.66,0,0,1);
            -ms-animation: schdBannerPagePulse 1.6s infinite cubic-bezier(.66,0,0,1);
            animation: schdBannerPagePulse 1.6s infinite cubic-bezier(.66,0,0,1);
            pointer-events: none;
        }
    }
}


.icon-shake {
    -webkit-animation-name: spaceboots;
    -webkit-animation-duration: 0.8s;
    -webkit-transform-origin:50% 50%;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
}

@-webkit-keyframes spaceboots {
    0% { -webkit-transform: translate(2px, 1px) rotate(0deg); }
    10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); }
    20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); }
    30% { -webkit-transform: translate(0px, 2px) rotate(0deg); }
    40% { -webkit-transform: translate(1px, -1px) rotate(1deg); }
    50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); }
    60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); }
    70% { -webkit-transform: translate(2px, 1px) rotate(-1deg); }
    80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); }
    90% { -webkit-transform: translate(2px, 2px) rotate(0deg); }
    100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); }
}
.shake:hover,
.shake:focus {
    -webkit-animation-name: spaceboots;
    -webkit-animation-duration: 0.8s;
    -webkit-transform-origin:50% 50%;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
}



