
.section-contact-area {
	position: relative;
	display: block;
	width: 100%;
	padding-top: 40px;
	padding-bottom: 50px;
	&.bg-gradient-01 {
		&:before {
			content: '';
			position: absolute;
			width: 100%;
			height: 378px;
			left: 0;
			top: 0;
			background: linear-gradient(0deg, #FFFFFF 35.21%, #E6EEF4 108.19%);
		}
	}
	@media (min-width: 992px) {
		padding-bottom: 30px;
		background: none;
	}
	&-inner {
		position: relative;
		display: block;
		@media (min-width: 1400px) {
			padding: 0 50px;
		}
	}

}


.contact-rows {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	flex-direction: vertical;
	@media (min-width: 992px) {
		flex-direction: row;
	}
	.cell-01 {
		width: 100%;
		@media (min-width: 992px) {
			width: 60%;
		}
	}
	.cell-02 {
		width: 100%;
		@media (min-width: 992px) {
			order: 3;
			margin-top: 50px;
		}
	}
	.cell-03 {
		width: 100%;
		@media (min-width: 992px) {
			width: 40%;
			order: 2;
		}
	}
	// End style layout column


	.contact-info-area {
		margin-bottom: 30px;
		font-size: 16px;
		line-height: 1.5;
		color: #333333;
		@media (min-width: 992px) {
			padding-top: 25px;
			padding-right: 150px;
			margin-bottom: 0;
		}
		.contact-inner {
			display: block;
			width: 100%;
			margin: 0 auto;
			overflow: hidden;
		}
		.contact-title-2 {
			font-size: 19px;
			line-height: 1.3;
			font-weight: 600;
			color: #333333;
		}
		.contact-info {
			color: #707070;
			p {
				display: table;
				margin-bottom: 8px;
				span {
					display: inline-block;
				}
			}
			i, svg {
				display: table-cell;
				width: 30px;
				padding-left: 0;
				padding-top: 5px;
				font-size: 16px;
				text-align: center;
			}
		}
	}

	.contact-from-area {
		position: relative;
		display: block;
		padding: 20px;
		background: #FFFFFF;
		box-shadow: 0px 2px 10px 5px rgba(0, 0, 0, 0.03);
		border-radius: 10px;
		.contact-form {
			position: relative;
			display: block;
			width: 100%;
			font-size: 16px;
			line-height: 1.5;
			color: #242732;
			::-webkit-input-placeholder { /* Edge */
				color: #8D8D8D;
			}
			:-ms-input-placeholder { /* Internet Explorer 10-11 */
				color: #8D8D8D;
			}
			::placeholder {
				color: #8D8D8D;
				font-size: 15px;
			}
			.form-title {
				position: relative;
				display: inline;
				width: auto;
				padding-bottom: 6px;
				margin-bottom: 30px;
				font-weight: bold;
				font-size: 20px;
				line-height: 22px;
				color: #fff;
			}
			label {
				padding-right: 0;
				font-size: 16px;
				line-height: 17px;
				color: #fff;
			}
			.form-control {
				font-size: 16px;
				border: 1px solid #D6D6D6;
				color: #333;
				&:focus {
					color: #333;
					background-color: #fff;
					outline: 0;
					box-shadow: none;
					border: 1px solid #f27227;
				}
			}
			.wrap-select {
				position: relative;
				&:after {
					content: "\f107";
					font-family: "Font Awesome 5 Pro";
					position: absolute;
					display: flex;
					justify-content: center;
					align-items: center;
					text-align: center;
					width: 30px;
					height: 34px;
					text-align: center;
					background: red;
					right: 15px;
					top: 0;
					bottom: 0;
					margin: auto;
					background: #fff;
					color: #707070;
				}
			}
			select:not(:valid) {
				color: #8D8D8D !important;
			}
			select:focus option[disabled] {
				color: #aaa;
			}
			textarea {
				max-height: 200px;
				min-height: 100px;
			}
			.btn-submit {
				position: relative;
				display: inline-block;
				min-width: 110px;
				padding: 7px 13px;
				margin: 0 0 10px;
				border: 1px solid #F27227;
				border-radius: 10px;
				background: #F27227;
				font-weight: normal;
				color: #fff;
				font-size: 16px;
				text-align: center;
				text-decoration: none;
				outline: none;
				box-shadow: none;
			}
		}
	}

	.contact-maps-area {
		position: relative;
		display: block;
		margin-top: 30px;
		margin-bottom: 30px;
		@media (min-width: 992px) {
			padding-top: 45px;
			margin-top: 50px;
			margin-bottom: 50px;
			border-top: 1px dashed rgba(112, 112, 112, .2);
		}
		.tabs-maps-area {
			.nav {
				display: flex;
				justify-content: flex-end;
				border: none;
				li {
					margin-left: 23px;
				}
				.nav-link {
					padding: 8px 0;
					border: none;
					background: none;
					&.active {
						font-weight: bold;
					}
				}
			}
			.tab-content {
				margin-top: 10px;
			}
		}
	}
}