
/* Start list Event dang dien ra Area css
============================================================================================ */
		.list-item-noslide {
			position: relative;
			display: flex;
			flex-wrap: wrap;
			@include gap(3,90px);
			row-gap: 30px;
			overflow: hidden;
			@media (max-width: 992px) {
				@include gap(2,20px);
			}
			@media (max-width: 410px) {
				@include gap(1,0px);
				row-gap: 20px;
			}
			@media (max-width: 991px) {
				font-size: 18px;
			}
			.item-slide {
				position: relative;
				display: block;
				min-height: 310px;
				.item-image {
					margin: 0 0 10px;
					overflow: hidden;
				}
				.item-info {
					padding: 0 10px;
					transition: all 0.2s ease-in-out;
					.info-name {
						margin-bottom: 5px;
						font-size: 22px;
						font-weight: bold;
						line-height: 1.3;
						color: #454545;
						@media (max-width: 991px) {
							font-size: 18px;
						}
						a {
							color: inherit;
							&:hover {
								color: inherit;
							}
						}
					}
					.info-description {
						display: none;
						font-size: 14px;
						line-height: 1.6;
						color: #FFFFFF;
						letter-spacing: 0.8px;
						-webkit-box-orient: vertical;
						text-overflow: ellipsis;
						-webkit-line-clamp: 3;
						word-break: break-word;
						overflow: hidden;
					}
					.info-datetime {
						font-size: 14px;
						color: #000000;
					}
					.btn-view-detail {
						display: none;
						width: 105px;
						height: 34px;
						font-size: 14px;
						line-height: 1.5;
						font-weight: 600;
						text-transform: uppercase;
						background: #01D49E;
						color: #FFFFFF;
						border-radius: 8px;
					}
				}
				&:hover {
					.item-info {
						display: flex;
						flex-direction: column;
						justify-content: center;
						position: absolute;
						top: 15px;
						left: 0;
						right: 0;
						width: calc(100% - 20px);
						height: calc(100% - 20px);
						min-height: 280px;
						padding: 0 30px;
						margin: 0 auto;
						background-color: rgba(0,0,0,.8);
						border-radius: 5px;
						.info-name {
							margin-bottom: 10px;
							color: #FFFFFF;
						}
						.info-description {
							display: block;
							display: -webkit-box;
						}
						.info-datetime {
							font-size: 22px;
							color: #01D49E;
							letter-spacing: 1.6px;
						}
						.btn-view-detail {
							display: block;
						}
					}
				}
			}
		}
/* End list Event dang dien ra Area css
============================================================================================ */