


	.section-unit-page {
		position: relative;
		display: block;
		padding: 20px 0;
		background-color: #FFFFFF;
		overflow: hidden;
		@media (min-width: 768px) {
			padding: 50px 0;
			background-color: #F5F6F7;
			background: linear-gradient(0deg, #EBF1F6 35.21%, #E6EEF4 108.19%);
			&.bg_mask {
				background-image: url('../images/bg_mask_unit.png');
				background-repeat: no-repeat;
				background-size: 100% auto;
				background-position: bottom center;
			}
		}
		.unit-inner {
			position: relative;
			display: block;
	        @media (min-width: 1400px) {
	            padding: 0 50px;
	        }
		}

		.home-box-title { 
			position: relative;
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: 100%;
			margin-bottom: 10px;
			.title-name {
				display: inline-block;
				padding-right: 20px;
				margin-bottom: 0;
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.3;
				text-align: justify;
				margin-bottom: 25px;
			}
			.btn-viewmore {
				color: #F27227;
				&:after {
					content: "\f178";
					position: relative;
					display: inline-block;
					top: 0;
					right: 0;
					width: 35px;
					height: 35px;
					line-height: 35px;
					text-align: center;
					vertical-align: middle;
					z-index: 2;
					font-family: 'Font Awesome 5 Pro';
					font-size: 16px;
					font-weight: 500;
					-moz-osx-font-smoothing: grayscale;
					-webkit-font-smoothing: antialiased;
					font-style: normal;
					font-variant: normal;
					text-rendering: auto;
				}
			}
			.title-unit {
				display: flex;
				flex-direction: column;
				// transform: translateX(-50px);
				margin-bottom: 20px;
				.title-small {
					font-size: 20px;
					font-weight: 600;
					@media (max-width: 767px) {
						font-size: 16px;
					}
				}
				.title-larger {
					font-weight: 800;
					font-size: 34px;
					@media (max-width: 767px) {
						font-size: 24px;
					}
				}
			}
		}
		.unit-box-title {
			position: relative;
			display: flex;
			align-items: center;
			padding: 5px 15px;
			margin-top: 30px;
			margin-bottom: 20px;
			font-size: 24px;
			font-weight: 600;
			color: #024173;
			background: #EBEBEB;
			border-radius: 10px;
			@media (min-width: 767px) {
				padding: 0;
			}
			.title-name {
				flex: none;
				display: inline-block;
				@media (min-width: 767px) {
					padding-right: 30px;
					background: #fff;
				}
			}
			@media (min-width: 767px) {
				background: transparent;
				&:after {
					content: '';
					flex: auto;
					position: relative;
					display: block;
					top: 0;
					bottom: 0;
					width: 100%;
					height: 1px;
					margin: auto;
					background-color: #DEDEDE;
					z-index: -1;
				}
			}
		}
		.filter-alphabet {
			display: flex;
			margin-bottom: 30px;
			ul {
				list-style: none;
				margin: 0;
				padding: 0;
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				font-size: 26px;
				li {
					margin: 1px;
					a {
						display: flex;
						align-items: center;
						justify-content: center;
						min-width: 44px;
						height: 44px;
						padding: 0 4px;
						border-radius: 5px;
						text-transform: uppercase;
						color: #707070;
						background-color: transparent;
						text-align: center;
						&:hover {
							background-color: rgba(#F27227,0.2);
						}
						&.active {
							color: #fff;
							background-color: #F27227;
							font-weight: bold;
						}
					}
				}
			}
		}
		// .filter-alphabet ~ .unit-list {
		// 	padding-top: 20px;
		// }
		.unit-box-list {
			position: relative;
			display: block;
			vertical-align: top;
			width: 100%;
			padding: 15px 0;
			text-align: center;
			.unit-list {
				position: relative;
				display: block;
				overflow: hidden;
				//padding: 0 30px;
				text-align: center;
				display: flex;
				flex-wrap: wrap;
				&.unit-list-01 {
					.unit-item {
						@media (min-width: 1200px) {
							width: calc(100% / 4  - 120px/4);
							margin-right: 40px;
							&:nth-child(4n) {
								margin-right: 0;
							}
						}
					}
				}
				&.unit-list-02 {
					.unit-item {
						@media (min-width: 1200px) {
							width: calc(100% / 5  - 160px/5);
							margin-right: 40px;
							&:nth-child(5n) {
								margin-right: 0;
							}
						}
					}
				}
				&.unit-list-03 {
					padding-top: 35px;
					.unit-item {
						@media (min-width: 1200px) {
							width: calc(100% / 6  - 200px/6);
							margin-right: 40px;
							&:nth-child(6n) {
								margin-right: 0;
							}
						}
					}
				}
				.unit-item {
					position: relative;
					display: flex;
					justify-content: center;
					width: calc(100% / 2  - 5px/2);
					padding: 10px;
					margin: 0 5px 10px 0;
					&:nth-child(2n) {
						margin-right: 0;
					}
					@media (min-width: 576px) {
						width: calc(100% / 3  - 20px/3);
						margin: 0 10px 10px 0;
						&:nth-child(2n) {
							margin-right: 10px;
						}
						&:nth-child(3n) {
							margin-right: 0;
						}
					}
					@media (min-width: 992px) {
						width: calc(100% / 4  - 30px/4);
						&:nth-child(3n) {
							margin-right: 10px;
						}
						&:nth-child(4n) {
							margin-right: 0;
						}
					}
					@media (min-width: 1200px) {
						width: percentage(1/5);
					}
					.item-inner {
						position: relative;
						display: block;
						.item-image {
							position: relative;
							display: flex;
							overflow: hidden;
							align-items: center;
							height: 100%;
							.item-image-a {
								position: relative;
								display: block;
								margin: 0 auto;
								filter: grayscale(1);
								box-shadow: none;
								&:hover {
									filter: grayscale(0);
								}
							}

							// start case 02
								img {
									max-width: 100%;
									max-height: 150px;
									margin: auto;
									// filter: grayscale(0.9) hue-rotate(45deg) saturate(0.9) opacity(0.4);
								}
								// &:hover {
								// 	img {
								// 		filter: unset;
								// 	}
								// }
							// end case 02
						}
					}
				}
			}
			.swiper-button-prev-unit,
			.swiper-button-next-unit {
				position: absolute;
				z-index: 2;
				display: block;
				display: flex;
				align-items: center;
				justify-content: center;
				top: 0;
				bottom: 0;
				margin: auto;
				width: 30px;
				height: 30px;
				background: transparent;
				text-align: center;
				line-height: 30px;
				border-radius: 50%;
				color: #fff;
				font-size: 24px;
				cursor: pointer;
				outline: none;
				&.swiper-button-disabled {
					cursor: default;
					opacity: 0;
				}
				&:hover {
					background: #eee;
					color: #333;
				}
			}
			.swiper-button-prev-unit {
				left: -40px;
			}
			.swiper-button-next-unit {
				right: -40px;
			}
		}
