		.btn-viewmore {
			position: relative;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			min-width: 150px;
			height: 40px;
			padding: 0 6px 0 12px;
			margin: 10px auto 30px;
			color: #F27227;
			background-color: transparent;
			border: 1px solid #F27227;
			border-radius: 5px;
			font-size: 14px;
			line-height: 1.3;
			font-weight: 600;
			text-align: center;
			text-decoration: none;
			cursor: pointer;
			&:hover {
				color: #F27227;
			}
		}
	}



	.wrap-partner-area {
		position: relative;
		display: block;
		clear: both;
		width: 100%;
		.area_inner {

		}
		.area_content {

		}
		.list-post {
			position: relative;
			display: block;
			clear: both;
			display: flex;
			flex-wrap: wrap;
			margin: 0 -15px;
			@media all and (max-width: 575px) {
				margin: 0 -7.5px;
			}
			.item-post {
				position: relative;
				width: percentage(1/4);
				padding: 0 15px;
				margin-bottom: 30px;
				vertical-align: top;
				@media all and (max-width: 991px) {
					width: percentage(1/2);
				}
				@media all and (max-width: 575px) {
					width: percentage(1/2);
					padding: 0 7.5px;
				}
				.box-image {
					position: relative;
					display: block;
					width: 100%;
					&:hover {
						.link-img {
							&:after {
								visibility: visible;
							}
							.read-more {
								visibility: visible;
							}
						}
					}
					.link-img {
						position: relative;
						display: block;
						border: 1px solid $border-title-highlight; 
						&:after {
							content: "";
							position: absolute;
							top:0;
							left: 0;
							width: 100%;
							height: 100%;
							background: #6c5b4d;
							opacity: .95;
							visibility: hidden;
						}
						.read-more {
							position: absolute;
							z-index: 2;
							width: 100px;
							height: 24px;
							top: 0;
							right: 0;
							bottom: 0;
							left: 0;
							margin: auto;
							font-size: 15px;
							font-weight: bold;
							text-align: center;
							color: #fff;
							border-bottom: 1px solid #fff;
							visibility: hidden;
						}
					}
				}
				.box-info {
					position: relative;
					display: block;
					width: 100%;
					padding-top: 15px;
					.box-title-item {
						position: relative;
						display: block;
						padding: 0;
						margin: 0;
						.title-post-item {
							position: relative;
							display: block;
							padding: 0 0 5px;
							margin: 0;
							font-size: 16px;
							font-weight: 700;
							color: #6c5b4d;
							white-space: nowrap;
							overflow: hidden;
							text-overflow: ellipsis;
						}
					}
					.box-description {
						p {
							line-height: 26px;
							color: #414042;
						}
						.read-more {
							display: block;
							margin: 8px 0 0;
							color: #6c5b4d;
							font-size: 13px;
							&:hover {
								text-decoration: underline;
							}
						}
					}
				}
			}
		}
	}