
/* Start Solution Eclick Detail Area css
============================================================================================ */
.section-solution-eclick-detail {
	position: relative;
	display: block;
	padding: 40px 0;
	background-color: #fff;
	.solution-inner {
		display: block;
		@media (min-width: 1400px) {
			padding: 0 50px;
		}
	}
	.solution-box-title {
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		z-index: 0;
		.title-name {
			display: inline-block;
			margin-bottom: 10px;
		}
	}
	.title-sub {
		position: relative;
		display: block;
		width: 100%;
		font-size: 19px;
		line-height: 1.3;
		text-align: justify;
		margin-bottom: 30px;
	}

	.box-nav-solution {
		display: none;
		position: relative;
		width: 100%;
		margin-bottom: 30px;
		@media (min-width: 768px) {
			display: block;
			position: sticky;
			top: 60px;
			z-index: 5;
			ul {
				position: relative;
				list-style: none;
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin: 0;
				padding: 0;
				width: 100%;
				border-radius: 20px;
				overflow: hidden;
				z-index: 0;
				&:before {
					content: '';
					position: absolute;
					display: block;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: rgba(132, 132, 132, .95);
					border: 2px solid rgba(255, 255, 255, 0.33);
					z-index: -1;
				}
				li {
					margin: 0;
					&:last-child {
						a {
							.menu-icon {
								width: 64px;
								height: 53px;
							}
						}
					}
					a {
						display: flex;
						align-items: center;
						min-height: 92px;
						padding: 0 30px;
						color: #fff;
						background: rgba(242, 114, 39, 0);
						border: 2px solid rgba(255, 255, 255, 0);
						border-radius: 20px;
						filter: grayscale(1);
						&:hover {
							background: rgba(242, 114, 39, .55);
							border: 2px solid rgba(255, 255, 255, 0.13);
							filter: grayscale(0.6);
						}
						&.active {
							background: rgba(242, 114, 39, .95);
							border: 2px solid rgba(255, 255, 255, 0.33);
							filter: grayscale(0);
						}
						.menu-icon {
							width: 53px;
							height: 53px;
							display: flex;
							align-items: center;
							justify-content: center;
							img,svg {
								max-width: 100%;
								height: auto;
							}
						}
						.menu-name {
							padding-left: 20px;
							font-size: 18px;
							font-weight: 600;
							line-height: 1.3;
							@media (min-width: 992px) {
								font-size: 23px;
							}
						}
					}
				}
			}
		}
	}
	.solution-box-list {
		position: relative;
		display: block;
		vertical-align: top;
		width: 100%;
		padding-top: 30px;
		@media (min-width: 992px) {
			padding-top: 60px;
		}
		.solution-list {
			position: relative;
			display: flex;
			flex-direction: column;
			margin: 0;
			.solution-item {
				position: relative;
				display: flex;
				flex-direction: column;
				width: 100%;
				padding: 0;
				margin-bottom: 30px;
				// background: #FFFFFF;
				// box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
				@media (min-width: 768px) {
					flex-direction: row;
					align-items: center;
					margin-bottom: 80px;
				}
				&:nth-child(2) {
					@media (min-width: 768px) {
						flex-direction: row-reverse;
					}
				}
				&:last-child {
					margin-bottom: 0;
				}
				$giai_phap_1_bg: #1169B0;
				$giai_phap_2_bg: #F27227;
				$giai_phap_3_bg: #16B14B;

				&:nth-child(2) {
					.solution-item-nth-1 {
						background: $giai_phap_2_bg;
						box-shadow: 8px 8px 13px rgba(242, 114, 39, .5);
						@media (min-width: 768px) {
							margin-right: 0;
							margin-left: 40px;
						}
						@media (min-width: 992px) {
							margin-right: 0;
							margin-left: 80px;
						}
					}
				}
				&:nth-child(3) {
					.solution-item-nth-1 {
						background: $giai_phap_3_bg;
						box-shadow: 8px 8px 13px rgba(22, 177, 75, .5);
					}
				}
				.solution-item-nth-1 {
					width: 100%;
					position: relative;
					overflow: hidden;
					min-width: 210px;
					padding: 25px 22px; 
					margin-bottom: 30px;
					display: flex;
					flex-direction: column;
					justify-content: space-between;
					align-items: stretch;
					background: $giai_phap_1_bg;
					color: #fff;
					border: 1px solid rgba(255, 255, 255, 0.5);
					border-radius: 10px;
					box-shadow: 8px 8px 13px rgba(17, 105, 176, .5);
					@media (min-width: 768px) {
						width: percentage(3.8/10);
						padding: 30px 30px; 
						margin-right: 40px;
					}
					@media (min-width: 992px) {
						margin-right: 80px;
					}
					&:nth-child(2) {
						background: $giai_phap_2_bg;
						box-shadow: 8px 8px 13px rgba(242, 114, 39, .5);
					}
					&:nth-child(3) {
						background: $giai_phap_3_bg;
						box-shadow: 8px 8px 13px rgba(22, 177, 75, .5);
					}

					.item-cell-img {
						margin-bottom: 10px;
						img,svg {
							width: 90px;
							height: auto;
							max-width: 100%;
						}
					}
					.item-cell-head {
						margin-bottom: 18px;
						font-size: 22px;
						line-height: 1.3;
						@media (min-width: 1200px) {
							font-size: 30px;
						}
						p {}
					}

					.item-cell-body {
						display: block;
						line-height: 1.4;
						p {
							margin: 0 0 8px;
						}
					}
				}
				.solution-item-nth-2 {
					width: 100%;
					position: relative;
					display: block;
					overflow: hidden;
					color: #333333;
					font-size: 16px;
					line-height: 1.3;
					@media (min-width: 768px) {
						width: percentage(6.2/10);
					}
					ul {
						margin: 0;
						padding: 0;
						list-style: none;
						li {
							position: relative;
							display: flex;
							margin: 0 0 .5rem;
							&:before {
								content: '';
								flex: none;
								position: relative;
								display: inline-block;
								top: 8px;
								width: 7px;
								height: 7px;
								margin-right: 15px;
								background: #F27227;
								border-radius: 50%;
								@media (min-width: 576px) {
									margin-right: 28px;
								}
							}
							p {
								margin-bottom: 8px;
							}
						}
					}
				}
			}
		}
	}
}
/* End Solution Eclick Detail Area css
============================================================================================ */



/* Start Solution Detail Area css
============================================================================================ */
.section-solution-detail {
	position: relative;
	display: block;
	padding: 40px 0;
	background-color: #fff;
	&.bg-solution {
		background: linear-gradient(-180deg, #EBF1F6 45.5%, #F5F5F5 100%);

		@media (max-width: 768px) {
			background-size: 140% auto !important;
		}
		@media (max-width: 420px) {
			background-size: 160% auto !important;
		}
	}
	.news-inner {
		display: block;
        @media (min-width: 1400px) {
            padding: 0 50px;
        }
	}
	.news-box-title {
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		z-index: 0;
		.title-name {
			display: inline-block;
			margin-bottom: 10px;
		}
	}
	.title-sub {
		position: relative;
		display: block;
		width: 100%;
		font-size: 19px;
		line-height: 1.3;
		text-align: justify;
		margin-bottom: 30px;
	}
	.news-box-list {
		position: relative;
		display: block;
		vertical-align: top;
		width: 100%;
		@media (max-width: 992px) {
			padding-top: 250px;
		}
		@media (max-width: 575px) {
			padding-top: 180px;
		}
		@media (max-width: 380px) {
			padding-top: 140px;
		}
		.news-list {
			position: relative;
			display: flex;
			flex-direction: column;
			margin: 0;

			.news-item {
				position: relative;
				display: block;
				width: 100%;
				margin-bottom: 20px;
				@media (min-width: 992px) {
					margin-bottom: 50px;
				}
				&:nth-child(1) {
					@media (min-width: 992px) {
						place-self: flex-end;
						width: 65%;
						margin-bottom: 83px;
					}
					.item-inner {
						.item-image {
							width: 100%;
							padding-right: 0;
						}
						.item-info {
							width: 100%;
							.item-title {
								margin-top: 10px;
							}
						}
					}
				}
				&:nth-child(2) {
					.item-inner {
						@media (min-width: 768px) {
							flex-direction: row;
						}
						.item-info {
							@media (min-width: 992px) {
								padding-left: 20px;
							}
							@media (min-width: 1400px) {
								padding-left: 60px;
							}
						}
					}
				}
				&:nth-child(3) {
					max-width: 780px;
					margin: 0 auto;
					.item-inner {
						@media (min-width: 768px) {
							flex-direction: row-reverse;
						}
						.item-info {
							@media (min-width: 992px) {
								padding-right: 20px;
							}
							@media (min-width: 1400px) {
								padding-right: 60px;
							}
						}
					}
				}
				.item-inner {
					position: relative;
					display: flex;
					flex-direction: column;
					padding: 0;
					// background: #FFFFFF;
					// box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
					.item-image {
						position: relative;
						display: block;
						width: 100%;
						overflow: hidden;
						margin-bottom: 20px;
						img {
							width: 100%;
							max-width: 100%;
							height: auto;
						}
					}
					.item-info {
						position: relative;
						display: block;
						width: 100%;
						overflow: hidden;
						.item-title {
							position: relative;
							display: block;
							width: 100%;
							margin: 0 0 10px;
							padding: 0;
							font-size: 18px;
							line-height: 1.3;
							color: #333333;
							font-weight: 600;
							@media (min-width: 992px) {
								font-size: 22px;
							}
						}
						.item-description {
							margin: 0 0 5px;
							color: #333333;
							font-size: 16px;
							line-height: 1.3;
							p {

							}
						}
					}
				}
			}
		}

	}
}
/* End Solution Detail Area css
============================================================================================ */
