.page-search {
	.section-search-list {
		position: relative;
		display: block;
		margin-top: 0;
		@media (min-width: 1400px) {
			margin-top: 45px;
		}
		.search-inner {
			display: block;
			padding-top: 20px;
			display: flex;
			flex-direction: column;
			gap: 30px;
			@media (min-width: 1400px) {
				padding: 0 50px;
			}
		}

		.wrap-box-title { 
			position: relative;
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: 100%;
			margin-bottom: 10px;
			.title-name {
				display: inline-block;
				padding-right: 20px;
				margin-bottom: 0;
			}
		}

		.wrap-form-search {
		    position: relative;
		    display: block;
		    width: 100%;
		    padding: 10px 0;
		    .form-search-2 {
		        position: relative;
		        display: block;
		        // background-color: #FFFFFF;
		        // border: 1px solid #DFE3ED;
		        // border-radius: 18px;
		        .form-group {
		            position: relative;
		            display: block;
		            width: 100%;
		            height: 36px;
		            background-color: #FFFFFF;
		            border: 1px solid #DFE3ED;
		            border-radius: 18px;
		            margin-bottom: 0;
		        }
		        .input-search {
		            float: left;
		            width: calc(100% - 44px);
		            height: 34px;
		            line-height: 34px;
		            padding: 0 15px;
		            border: 0;
		            border-radius: 20px 0 0 20px;
		            outline: none;
		            box-shadow: none;
		            background-color: #EDF2F3;
		            &::placeholder {
		                font-size: 12px;
		                color: $text-primary-10;
		                font-style: italic;
		                letter-spacing: -0.24px;
		            }
		        }
		        .btn-search {
		            position: relative;
		            float: left;
		            width: 44px;
		            height: 34px;
		            border: 0;
		            border-radius: 0 20px 20px 0;
		            outline: none;
		            box-shadow: none;
		            background-color: #EDF2F3;
                    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='17' height='17' viewBox='0 0 17 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M7.40092 0.201904C3.32009 0.201904 0 3.52395 0 7.60724C0 11.6905 3.32013 15.0126 7.40092 15.0126C11.4817 15.0126 14.8019 11.6905 14.8019 7.60724C14.8019 3.52395 11.4818 0.201904 7.40092 0.201904ZM7.40092 13.7952C3.98901 13.7952 1.21327 11.0191 1.21327 7.6072C1.21327 4.19507 3.98901 1.41918 7.40092 1.41918C10.8128 1.41918 13.5886 4.19507 13.5886 7.6072C13.5886 11.0191 10.8129 13.7952 7.40092 13.7952Z' fill='%23707070'/%3e%3cpath d='M16.604 15.9608L12.6204 11.9842C12.3828 11.7472 11.9988 11.7482 11.7624 11.9864C11.5262 12.2248 11.5272 12.6101 11.7646 12.8473L15.7482 16.8238C15.8667 16.9419 16.0212 17.001 16.1761 17.001C16.332 17.001 16.4877 16.9411 16.6062 16.8216C16.8424 16.5832 16.8414 16.1979 16.604 15.9608Z' fill='%23707070'/%3e%3c/svg%3e");
                    background-repeat: no-repeat;
                    background-position: center;
		            &:before {
		                content: "";
		                position: absolute;
		                display: block;
		                top: 0;
		                bottom: 0;
		                left: 0;
		                margin: auto;
		                width: 1px;
		                height: 10px;
		                background-color: #DFE3ED;
		            }
		        }
		    }
		}
		.list-item-search {
			.news-list {
				position: relative;
				display: block;
				overflow: hidden;
				.news-item {
					position: relative;
					display: block;
					clear: both;
					width: 100%;
					padding: 15px 0;
					margin: 0;
					// border-bottom: 1px solid #EEEEEE;
					overflow: hidden;
					&:nth-child(1) {
						padding-top: 0;
					}
					&.item-top {
						padding: 0;
						border-bottom: 0;
						.item-inner {
							.item-image {
								width: 100%;
							}
							.item-info {
								width: 100%;
							}
							@media (min-width: 576px) {
								flex-direction: column;
							}
							@media (min-width: 768px) {
								flex-direction: row;
							}
							@media (min-width: 992px) {
								gap: 0;
								.item-image {
									width: percentage(2/3);
								}
								.item-info {
									width: percentage(1/3);
									background: rgba(196, 196, 196, 0.1);
									padding: 20px 15px 10px 34px;
									.item-title {
										margin: 0 0 10px;
									}
									.item-description {
										margin: 0 0 15px;
									}
								}
							}
						}
					}
					.item-inner {
						position: relative;
						display: flex;
						flex-direction: column;
						padding: 0;
						gap: 15px;
						// background: #FFFFFF;
						// box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
						@media (min-width: 576px) {
							flex-direction: row;
						}
						.item-image {
							position: relative;
							display: block;
							overflow: hidden;
							width: 100%;
							@media (min-width: 576px) {
								width: percentage(1/3);
							}
							.item-image-a {
								position: relative;
								display: block;
								margin: 0 auto;
								@media (max-width: 575px) {
									min-height: 170px;
									img {
										width: 100%;
										height: 100%;
										object-fit: cover;
									}
								}
							}
						}
						.item-info {
							position: relative;
							display: block;
							background: #fff;
							width: 100%;
							padding: 0;
							@media (min-width: 576px) {
								width: percentage(2/3);
							}
							.item-title {
								position: relative;
								display: block;
								width: 100%;
								margin: 0 0 5px;
								padding: 0;
								.item-title-a {
									position: relative;
									display: block;
									display: -webkit-box;
									height: auto;
									padding: 0;
									margin: 0;
									font-weight: 600;
									font-size: 18px;
									line-height: 1.3;
									color: #333333;
									text-decoration: none;
									overflow: hidden;
									@media (min-width: 1200px) {
										font-size: 22px;
									}
								}
							}
							.item-description {
								display: block;
								height: 66px;
								margin: 0 0 6px;
								color: #333333;
								font-size: 14px;
								padding: 0;
								overflow: hidden;
								-webkit-box-orient: vertical;
								text-overflow: ellipsis;
								-webkit-line-clamp: 3;
								word-break: break-word;
								@media (min-width: 1200px) {
									height: 70px;
									font-size: 16px;
									margin: 0 0 10px;
								}
							}
							.publish-time {
								font-size: 14px;
							}
						}
					}
				}
			}

			.line-dot {
				width: 100%;
				height: 0;
				margin-top: 28px;
				margin-bottom: 28px;
				border-bottom: 1px dashed #B4B4B4;
			}
			.btn-viewmore {
				position: relative;
				display: block;
				width: 200px;
				height: 50px;
				line-height: 50px;
				margin: 20px auto;
				color: #ffffff;
				background: transparent;
				border: 1px solid #ffffff;
				border-radius: 5px;
				font-size: 18px;
				text-align: center;
				text-decoration: none;
			}
		}
		.area-sidebar-right {
			.img-ads {
				text-align: center;
				position: sticky;
				top: 0px;
				margin-bottom: 20px;
				&.box-sticky {
					position: sticky;
					top: 0px;
					margin-bottom: 20px;
				}
				img {
					width: auto;
					margin: 0 auto;
					max-width: 100%;
				}
			}

			.box-bctc-area {
				position: relative;
				display: block;
				width: 100%;
				background: rgba(196, 196, 196, 0.1);
				.sidebar-title {
					position: relative;
					display: block;
					padding: 15px 25px;
					color: #333333;
					background: #D6D6D6;
					border-radius: 5px 5px 0 0;
					.title-name {
						margin: 0;
						font-size: 20px;
						line-height: 1.3;
						font-weight: bold;
						color: inherit;
					}
				}
				.list-item-post {
					position: relative;
					display: flex;
					flex-wrap: wrap;
					flex-direction: column;
					width: 100%;
					padding: 25px 25px 0;
					.item-post { 
						position: relative;
						display: flex;
						flex-direction: column;
						width: 100%;
						margin: 0 0 25px;
						padding: 0 0 20px;
						font-size: 16px;
						border-bottom: 1px dashed #B4B4B4;
						&:after {
							border: 1px dashed #B4B4B4;
						}

						.item-info { 
							position: relative;
							width: 100%;
							.info-title { 
								position: relative;
								width: 100%;
								margin: 0 0 15px;
								font-size: 16px;
								line-height: 1.3;
								font-weight: 600;
								color: #333333;
							}
							.publish-time {
								padding: 5px 0;
								color: #707070;
								font-size: 14px;
							}
							.btn-download-file {
								display: inline-flex;
								align-items: center;
								justify-content: center;
								font-size: 16px;
								line-height: 1.3;
								font-weight: 600;
								color: #F27227;
								text-decoration: none;
								cursor: pointer;
								&:after {
									content: "\f175";
									position: relative;
									padding-left: 10px;
									text-align: center;
									vertical-align: middle;
									z-index: 2;
									font-family: 'Font Awesome 5 Pro';
									font-size: 16px;
									font-weight: 500;
									-moz-osx-font-smoothing: grayscale;
									-webkit-font-smoothing: antialiased;
									font-style: normal;
									font-variant: normal;
									text-rendering: auto;
								}
							}
						}
					}
				}
				.btn-viewmore {
					position: relative;
					display: inline-flex;
					align-items: center;
					justify-content: center;
					min-width: 150px;
					height: 40px;
					padding: 0 6px 0 12px;
					margin: 10px auto 30px;
					color: #F27227;
					background-color: transparent;
					border: 1px solid #F27227;
					border-radius: 5px;
					font-size: 14px;
					line-height: 1.3;
					font-weight: 600;
					text-align: center;
					text-decoration: none;
					cursor: pointer;
				}
			}
		}

	}
}