:root {

    --primary-1:            #1169B0;    // Color gold
    --primary-2:            #333333;    // Color background menu / text default
    --primary-3:            #ed3011;    // Color price
    --primary-4:            #FFFFFF;    // Color background default
    --primary-5:            #000000;    // Color black
    --primary-6:            #3C3241;    // Color text default
    --primary-7:            #212121;    // Color text highlight
    --primary-8:            #FF4848;    // Color red
    --primary-9:            #757575;    // Color text description
    --primary-10:           #635B67;    // Color text header/footer

    --border:               #CDD9ED;
    --border-hover:         #99A3BA;
    --text-color:           var(--primary-2);
    --text-color-highlight: var(--primary-7);
    --text-color-headline:  var(--primary-6);
    --text-color-price:     var(--primary-3);

    --shadow:               rgba(22, 30, 50, .04);

}

/*Color Variables*/
/*=================== group color F2F5F8 ====================*/
$bgbody:                    var(--primary-3);
$bghome:                    var(--primary-3); //background home page
$border-title:              var(--primary-3);

$baseColor:                 var(--primary-1);
$dip:                       #222222;
$pfont:                     var(--primary-7);

$text-color:                var(--text-color);              //color default; 
$text-color-highlight:      var(--text-color-highlight);    //color highlight; 

/*=================== group color ed3011 ====================*/
$price:                     var(--primary-3); // txt-color price

/*=================== group color 635B67 ====================*/
$text-header:               var(--primary-10);
$text-primary-10:           var(--primary-10);

$bg_trang:                  var(--primary-4); //background white
$txt_trang:                 var(--primary-4); //text white
$border_trang:              var(--primary-4); //border white
$trang:                     var(--primary-4); //color white
$den:                       var(--primary-5); //background den
$do:                        var(--primary-8); //color price
$color-title:               var(--primary-7); //color title post


/*=================== group color 757575 ====================*/
$color-subtitle:            var(--primary-9); //color title post
$color-desciption:          var(--primary-9); //color title post
$color-primary-9:           var(--primary-9); //color title post

/*=================== group color 3c2313 ====================*/
$bgbds:                     var(--primary-2); // background bds
$bgfooter:                     var(--primary-2); // background bds
$bgmenu:                    var(--primary-2);
$txt_default:               var(--primary-2); //text default

/*=================== group color 3C3241 ====================*/
$color-title-post:          var(--text-color-headline); // txt-color title
$color-title-headline:      var(--text-color-headline); // txt-color headline

/*=================== group color d2994f ====================*/
$border-title-highlight:    var(--primary-1); //color border title
$bg-btn:                    var(--primary-1); // background-color button
$bg-color:                  var(--primary-1); // background-color button
$color-highlight:           var(--primary-1); // txt-color highlight hover


$color-redl:                var(--primary-8);
$color-greenl:              var(--primary-1);


