// Mixins
@mixin transition($property: all, $duration: 300ms, $animate: linear, $delay:0s){
    transition: $property $duration $animate $delay; 
}

// Placeholder Mixins
@mixin placeholder {
  &.placeholder { @content; }
  &:-moz-placeholder { @content; }
  &::-moz-placeholder { @content; }
  &::-webkit-input-placeholder { @content; }
}


* {
    box-sizing: border-box;
    backdrop-filter: none;
    backface-visibility: hidden;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font-family: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
html {
    position: relative;
    display: block;
    width: 100%;
    max-width: 100%;
    overflow-x: hidden;
    -webkit-font-smoothing: antialiased;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}

body.fptOnline {
    font-family: $BaiJam;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.5;
    color: var(--text-color);
    background-color: #fff;
    word-break: break-word;
    word-wrap: break-word;
    scroll-behavior: smooth;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    &::-webkit-scrollbar {
        width: 8px;
        height: 8px;
        background-color: #fff;
    }
    &::-webkit-scrollbar-thumb {
        background-color: #333;
    }
    &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px rgba(0,0,0,.3);
        background-color: #fff;
    }

    ol, ul {
        list-style: none;
    }

    h1, h2, h3, h4, h5, h6{ 
        font-weight: bold;
    }
    b, strong {
        font-weight: bold;
    }
    .font-weight-medium {
        font-weight: 500;
    }
    .bold,
    .font-weight-bold {
        font-weight: bold;
    }
    .normal,
    .font-weight-normal {
        font-weight: normal;
    }
    a {
        color: #3c3241;
        text-decoration:none;
        outline: none;
        transition: all 0.2s ease-in-out;
        // user-select: all;
        &:hover, &:focus{
           text-decoration:none;
           outline: none;
        }
        &:hover {
            color: #1169b0;
        }
    }
    .row.m0{
        margin: 0px;
    }
}

// Start responsive images
    img {
        width: initial;
        max-width: 100%;
        height: auto;
        vertical-align: middle;
        border-style: none;
    }
    img.responsive {
        position: relative;
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    img.scaledown {
        position: relative;
        display: block;
        width: 100%;
        height: 100%;
        object-fit: scale-down;
    }
    img.contain {
        position: relative;
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
    img.img-responsive {
        width: 100%;
        height: 100%;
        clear: both;
        object-fit: cover;
    }
    img.img-initial {
        width: initial !important;
        height: initial !important;
        clear: both;
    }
    img.img-resize {
        position: relative;
        display: block;
        margin: auto;
        width: auto;
        height: auto;
        max-width: 100%;
        max-height: 100%;
        clear: both;
    }
    img:not([src]) {
        visibility: hidden;
    }
    /* Fixes Firefox anomaly during image load */
    @-moz-document url-prefix() {
        img:-moz-loading {
            visibility: hidden;
        }
    }
// End responsive images


// Start Chia cot theo flex - gap
    @mixin gap($column,$gap) {
        $number_gap:        $column - 1;
        $total_gap:         #{$gap * $number_gap};
        $clear_margin:      $total_gap / $column;
        gap:                $gap;
        &>* {
            width: calc(#{percentage(1/$column)} - #{$clear_margin});
        }
    }

    // .example {
