

/* Menu mobile area css
============================================================================================ */
.modal-backdrop {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1040;
    background-color: #000;
    overflow: hidden;
}
.modal-nav-side-menu {
    padding: 0 !important;
    .modal-dialog {
        left: 0;
        float: left;
        margin: 0;
        height: 100%;
        width: 90%;
        max-width: 500px;

        @media all and (max-width: 580px) {
            left: 0;
            right: auto;
            float: left;
        }
        @media all and (max-width: 355px) {
            width: 100%;
            max-width: 100%;
        }
        .modal-content {
            height: 100%;
            max-height: 100%;
            overflow: auto;
            border: 0;
            border-radius: 0;
        }
    }

    .nav-side-menu {
        position: fixed;
        display: block;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: #fefeff;
        color: #000;
        z-index: 999;
        overflow: auto;
        font-family: inherit;
        font-size: 16px;
        pointer-events: auto;
        &.show {
            display: block;
        }
        .btn-close-nav {
            position: fixed;
            display: block;
            top: 0;
            right: 0;
            width: 34px;
            height: 34px;
            line-height: 34px;
            text-align: center;
            background: rgba(204, 204, 204, 0.5);
            color: #1a1a1a;
            font-size: 16px;
            z-index: 2;
            cursor: pointer;
            svg {
                path {
                    fill: transparent;
                    stroke: currentColor;
                    stroke-linejoin: round;
                    stroke-width: 3;
                }
            }
        }
        .box-logo-nav {
            position: relative;
            display: block;
            width: 100%;
            padding: 15px 30px 8px;
            overflow: hidden;
            text-align: center;
            background-color: rgba(108, 91, 77, .1);
            .m-logo {
                position: relative;
                display: block;
                margin: 0;
                font-family: 'Averta-Black';
                font-style: normal;
                font-size: 21px;
                line-height: 32px;
                text-transform: uppercase;
                color: #1aa7dd;
                display: flex;
                justify-content: center;
                flex-direction: column;
                span { 
                    white-space: nowrap;
                    font {
                        color: #194689;
                        font-size: 30px;
                    }
                }
                img {
                    max-width: 100%;
                    max-height: 120px;
                    @media all and (max-width: 580px) {
                        max-width: 100%;
                        max-height: 85px;
                    }
                }
                svg {
                    max-width: 100%;
                    max-height: 70px;
                }
            }
        }
        .menu-list {
            position: relative;
            display: block;
            list-style: none;
            width: 100%;
            padding: 10px 20px 0 35px;
            margin: 0px;
            background: #fff;
            overflow: hidden;
            overflow-y: auto;
            user-select: none;
            &>.item-menu-cate {
                position: relative;
                display: block;
                list-style: none;
                margin: 0px;
                width: 100%;
                &:not(:last-child) {
                    border-bottom: 1px solid rgba(196, 196, 196, 0.2);
                }
                a {
                    position: relative;
                    display: block;
                    padding: 20px 0;
                    font-size: 17px;
                    color: #3C3241;
                    &:focus {
                        color: $color-highlight;
                    }
                    .menu-icon {
                        width: 35px;
                    }
                    svg {
                        path {
                            fill: $color-highlight;
                        }
                    }
                }

                .btn-toggle {
                    position: absolute;
                    top: 0;
                    right: 0;
                    z-index: 2;
                    background: #1aa7dd;
                    border-left: 1px solid #eaeaea;
                    width: 50px;
                    height: 48px;
                    cursor: pointer;
                    &:before {
                        content: "";
                        position: absolute;
                        display: block;
                        top: 23px;
                        left: 15px;
                        width: 20px;
                        height: 2px;
                        background: #fff;
                    }
                    &:after {
                        content: "";
                        position: absolute;
                        display: block;
                        top: 14px;
                        left: 24px;
                        width: 2px;
                        height: 20px;
                        background: #fff;
                    }
                    &.active {
                        &:after {
                            content: "";
                            visibility: hidden;
                        }
                    }
                }
                .menu-list-lv2 {
                    position: relative;
                    display: none;
                    list-style: none;
                    width: 100%;
                    padding: 0 0 0 40px;
                    margin: 0px;
                    background: #fff;
                    overflow: hidden;
                    opacity: 0;
                    -ms-transform: translate(35px, 0);
                    transform: translate(35px, 0);
                    transition: transform 0.3s ease, opacity 0.3s ease;
                    &.active {
                        display: block;
                        -ms-transform: translate(0, 0);
                        transform: translate(0, 0);
                        opacity: 1;
                    }
                    &>.item-menu-cate {
                        position: relative;
                        display: block;
                        list-style: none;
                        margin: 0px;
                        width: 100%;
                        border-top: 1px solid #ebebeb;
                        a {
                            width: calc(100% - 50px);
                            font-size: 15px;
                            text-transform: unset;
                        }
                    }
                }
            }
        }

        strong {
            font-weight: 700;
            color: #0064b2;
        }
    }

    @media all and (max-width: 355px) {

        .nav-side-menu .menu-list > .item-menu-cate a {
            padding: 6px 10px;
        }
        .nav-side-menu .menu-list > .item-menu-cate .menu-list-lv2 > .item-menu-cate {
            .menu-list-lv2 {
                padding-left: 25px;
            }
        }
        .nav-side-menu .menu-list > .item-menu-cate .menu-list-lv2 > .item-menu-cate a {
            width: calc(100% - 40px);
        }
        .nav-side-menu .menu-list > .item-menu-cate .btn-toggle {
            width: 40px;
            height: 40px;
            &:before {
                content: "";
                top: 19px;
                left: 11px;
                width: 18px;
            }
            &:after {
                content: "";
                top: 11px;
                left: 19px;
                height: 18px;
            }
        }
    }
}

/* End Menu mobile area css
============================================================================================ */
