
/* Start About Us css
============================================================================================ */
.section-about-us-area {
	position: relative;
	display: block;
	padding: 30px 0;
	.section-inner {
		padding: 0;
		@media (min-width: 1400px) {
			padding: 0 50px;
			margin-top: 30px;
		}
	}
	.section-box-title {
		position: relative;
		display: flex;
		flex-direction: column;
		width: 100%;
		color: #454545;
		.title-name {
			position: relative;
			display: flex;
			flex-direction: column;
			margin-bottom: 20px;
		}
		.title-sub {
			position: relative;
			display: block;
			width: 100%;
			height: auto;
			font-size: 16px;
			line-height: 1.4;
			margin-bottom: 25px;
		}
	}
	.section-list-items {
		width: 100%;
		.wrap-slide {
			position: relative;
		}
		.list-item-slide {
			position: relative;
			overflow: hidden;
		}
	}

	.wrap-nav-tab {
		@media (max-width: 991px) {
			flex-direction: column;
		}
		.nav {
			flex: none;
			width: 100%;
			padding-right: 0;
			margin-bottom: 30px;
			@media (min-width: 992px) {
				width: 200px;
				padding-right: 10px;
			}
			.nav-link {
				flex: none;
				padding: 8px 0;
				color: #333333;
				outline: none;
				background: none;
				border: none;
				text-align: left;
				&:before {
					content: '';
					display: inline-block;
					flex: none;
					width: 8px;
					height: 8px;
					margin: 12px 15px 0 0;
					background: #C4C4C4;
					border-radius: 8px;
					transition: all 200ms ease-in-out;
				}
				&.active {
					display: flex;
					color: #F27227;
					&:before {
						width: 33px;
						height: 3px;
						margin: 12px 15px 0 0;
						background-color: #F27227;
					}
				}
			}
		}
		.tab-content {
			.tab-pane {
				&-inner {
				}
			}
		}
		.tab-pane-inner {
			&.tab-pane-01 {
				.wrap-item {
					position: relative;
					@media (min-width: 1200px) {
						padding-right: 90px;
					}
					.item-inner {
						position: relative;
						display: flex;
						flex-direction: column;
						padding: 0;
						@media (min-width: 992px) {
							flex-direction: row;
							align-items: center;
						}
						.item-image {
							position: relative;
							display: block;
							width: 100%;
							overflow: hidden;
							flex: none;
							margin-bottom: 30px;
							@media (min-width: 768px) {
								margin-bottom: 0;
							}
							@media (min-width: 992px) {
								width: 130px;
								margin-right: 50px;
							}
							.item-image-a {
								position: relative;
								display: block;
								margin: 0 auto;
							}
						}
						.item-info {
							position: relative;
							display: block;
							width: 100%;
							max-width: 750px;
							background: #fff;
							@media (min-width: 992px) {
								padding-left: 32px;
							}
							.item-title {
								position: relative;
								display: block;
								width: 100%;
								margin: 0 0 10px;
								padding: 0 30px;
								font-size: 18px;
								line-height: 1.3;
								font-weight: normal;
								color: #333333;
								@media (min-width: 768px) {
									padding: 0;
								}
								@media (min-width: 992px) {
									font-size: 26px;
								}
								&.icon-quotes {
									&:before {
										content: '';
										position: absolute;
										display: block;
										top: 0;
										left: 0;
										width: 24px;
										height: 24px;
										background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='25' height='21' viewBox='0 0 25 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M6.21094 20.3C8.86094 20.3 10.7609 18.4 10.7609 15.45C10.7609 12.75 8.71094 11.05 6.21094 11.05C6.01094 11.05 5.86094 11.05 5.66094 11.1C5.66094 7.8 7.36094 5.55 10.9109 3.9L9.01094 -1.54972e-06C3.31094 2.8 0.560938 7 0.560938 12.5C0.560938 17.45 2.51094 20.3 6.21094 20.3ZM20.2109 20.3C22.8609 20.3 24.7609 18.4 24.7609 15.45C24.7609 12.75 22.7109 11.05 20.2109 11.05C20.0109 11.05 19.8609 11.05 19.6609 11.1C19.6609 7.8 21.3609 5.55 24.9109 3.9L23.0109 -1.54972e-06C17.3109 2.8 14.5609 7 14.5609 12.5C14.5609 17.45 16.5109 20.3 20.2109 20.3Z' fill='%23707070'/%3e%3c/svg%3e");
										background-repeat: no-repeat;
										background-size: 80%;
										@media (min-width: 768px) {
											left: -32px;
											width: 32px;
											height: 32px;
											background-size: initial;
										}
									}
									&:after {
										content: '';
										position: relative;
										display: inline-block;
										top: 5px;
										right: -5px;
										width: 24px;
										height: 24px;
										background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='25' height='21' viewBox='0 0 25 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M6.21094 20.3C8.86094 20.3 10.7609 18.4 10.7609 15.45C10.7609 12.75 8.71094 11.05 6.21094 11.05C6.01094 11.05 5.86094 11.05 5.66094 11.1C5.66094 7.8 7.36094 5.55 10.9109 3.9L9.01094 -1.54972e-06C3.31094 2.8 0.560938 7 0.560938 12.5C0.560938 17.45 2.51094 20.3 6.21094 20.3ZM20.2109 20.3C22.8609 20.3 24.7609 18.4 24.7609 15.45C24.7609 12.75 22.7109 11.05 20.2109 11.05C20.0109 11.05 19.8609 11.05 19.6609 11.1C19.6609 7.8 21.3609 5.55 24.9109 3.9L23.0109 -1.54972e-06C17.3109 2.8 14.5609 7 14.5609 12.5C14.5609 17.45 16.5109 20.3 20.2109 20.3Z' fill='%23707070'/%3e%3c/svg%3e");
										background-repeat: no-repeat;
										background-size: 80%;
										transform: rotateZ(180deg);
										@media (min-width: 768px) {
											width: 32px;
											height: 32px;
											background-size: initial;
										}
									}
								}
							}
							.item-description {
								margin: 0 0 5px;
								color: #333333;
								font-size: 16px;
								padding: 0;
							}
							.item-time-publish {
								margin: 0 0 5px;
								color: #707070;
								font-size: 14px;
							}
							.btn-readmore {
								position: relative;
								display: inline-flex;
								padding: 8px 0;
								margin: 0;
								color: #F27227;
								font-size: 16px;
								line-height: 1.3;
								font-weight: 600;
								text-decoration: none;
								&:after {
									content: "\f178";
									position: relative;
									padding-left: 8px;
									text-align: center;
									vertical-align: middle;
									z-index: 2;
									font-family: 'Font Awesome 5 Pro';
									font-size: 15px;
									-moz-osx-font-smoothing: grayscale;
									-webkit-font-smoothing: antialiased;
									font-style: normal;
									font-variant: normal;
									text-rendering: auto;
								}
							}
						}
					}
				}
			}
			&.tab-pane-02 {

				.wrap-list {
					position: relative;
					display: flex;
					flex-direction: column;
					flex-wrap: wrap;
					@include gap(1,0px);
					overflow: hidden;

					@media (min-width: 767px) {
						flex-direction: row;
						@include gap(2,0px);
					}
					@media (min-width: 992px) {
						@include gap(2,0px);
					}
					.wrap-item {
						position: relative;
						display: flex;
						margin: 0 0 40px;
						@media (min-width: 767px) {
							padding: 15px;
						}
						@media (min-width: 992px) {
							margin: 0 0 15px;
						}
						.item-inner {
							position: relative;
							display: flex;
							flex-wrap: wrap;
							justify-content: flex-end;
							align-items: center;
							background: #FFFFFF;
							@media (min-width: 767px) {
								align-items: flex-start;
								padding: 34px 18px 15px;
								box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.05);
								border-radius: 9px;
							}
							.item-image {
								flex: none;
								position: relative;
								display: block;
								width: 120px;
								overflow: hidden;
								padding-right: 20px;
								@media (min-width: 1200px) {
									position: absolute;
									left: 20px;
									top: 40px;
								}
								img {
									max-width: 100%;
									width: initial;
									height: auto;
								}
							}
							.item-title {
								position: relative;
								display: block;
								width: calc(100% - 130px);
								margin: 0 0 10px;
								padding: 0;
								font-size: 24px;
								line-height: 1.3;
								color: #333333;
								font-weight: 600;
							}
							.item-description {
								width: 100%;
								margin: 10px 0 5px;
								color: #333333;
								font-size: 16px;
								padding: 0;
								@media (min-width: 1200px) {
									width: calc(100% - 130px);
									margin: 0 0 5px;
								}
							}
						}
					}
				}
			}
			&.tab-pane-03 {

				.wrap-list {
					position: relative;
					display: block;
					padding: 0;
					margin: 0 0 20px;
					overflow: hidden;
					@media (min-width: 1200px) {
						padding: 20px;
					}
					.wrap-item {
						position: relative;
						display: block;
						width: 100%;
						margin: 0 0 40px;
						background: #FFFFFF;
						&:last-child {
							margin-bottom: 0;
						}
						.item-inner {
							position: relative;
							width: 100%;
							display: flex;
							flex-wrap: wrap;
							align-items: center;
							min-height: 100px;
							@media (min-width: 1200px) {
								justify-content: flex-end;
								align-items: flex-start;
							}
							.item-image {
								flex: none;
								position: relative;
								display: block;
								width: 120px;
								overflow: hidden;
								padding-right: 20px;
								@media (min-width: 1200px) {
									width: 200px;
									position: absolute;
									left: 0;
									top: 0;
								}
								img {
									max-width: 100%;
									width: initial;
									height: auto;
								}
							}
							.item-title {
								position: relative;
								display: block;
								width: calc(100% - 120px);
								margin: 0 0 10px;
								padding: 0;
								font-size: 21px;
								line-height: 1.3;
								color: #333333;
								font-weight: 600;
								@media (min-width: 992px) {
									width: calc(100% - 200px);
									font-size: 24px;
								}
							}
							.item-description {
								width: 100%;
								margin: 10px 0 5px;
								color: #333333;
								font-size: 16px;
								padding: 0;
								@media (min-width: 1200px) {
									width: calc(100% - 200px);
									margin: 0 0 5px;
								}
							}
						}
					}
				}
			}
			.title-tab {
				position: relative;
				display: block;
				margin: 20px 0 30px;
				font-weight: bold;
				font-size: 24px;
				color: #F27227;
			}
		}
	}
}
/* End About Us css
============================================================================================ */



/* Start About Lanh dao css
============================================================================================ */
.section-leader-area {
	position: relative;
	display: block;
	background: linear-gradient(180deg, #FFFFFF 35.21%, #E6EEF4 108.19%);
	.section-inner {
		padding: 10px 0;
		@media (min-width: 992px) {
			padding: 30px 0;
		}
		@media (min-width: 1400px) {
			padding: 40px 50px;
		}
	}
	.section-box-title {
		position: relative;
		display: flex;
		flex-direction: column;
		width: 100%;
		color: #454545;
		.title-name {
			position: relative;
			margin-bottom: 20px;
		}
		.title-sub {
			position: relative;
			display: block;
			width: 100%;
			height: auto;
			font-size: 16px;
			line-height: 1.4;
			margin-bottom: 28px;
		}
	}
	.section-list-items {
		width: 100%;
		.wrap-slide {
			position: relative;
		}
		.list-item-slide {
			position: relative;
			overflow: hidden;
			.item-slide {
				position: relative;
				display: block;
				.item-image {
					position: relative;
					margin: 0 0 15px;
					overflow: hidden;
					border-radius: 8px;
					text-align: center;
					&:before {
						content: '';
						position: absolute;
						width: 100%;
						height: 100%;
						top: 0;
						left: 0;
						background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='243' height='249' viewBox='0 0 243 249' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath opacity='0.1' d='M130.01 0C94.8822 0 64.462 20.796 52.149 50.0404C51.4247 51.665 49.2519 57.8388 49.2519 57.8388L0 248.902H112.99C151.739 248.902 184.694 223.882 193.748 190.088L243 0H130.01Z' fill='%23F27227'/%3e%3c/svg%3e");
						background-size: auto;
						background-repeat: no-repeat;
						background-position: bottom center;
					}
					>a {
						position: relative;
						display: block;
						overflow: hidden;
					}
					img {
						width: auto;
						height: auto;
						margin: 0 auto;
					}
				}
				.item-info {
					padding: 0 10px;
					text-align: center;
					transition: all 200ms ease-in-out;
					.info-name {
						margin-bottom: 5px;
						font-size: 19px;
						font-weight: bold;
						line-height: 1.3;
						color: #333333;
						@media (max-width: 991px) {
							font-size: 16px;
						}
						a {
							color: inherit;
							&:hover {
								color: inherit;
							}
						}
					}
					.info-description {
						display: block;
						font-size: 16px;
						line-height: 1.4;
						color: #707070;
					}
					.info-datetime {
						font-size: 14px;
						color: #000000;
					}
					.btn-view-detail {
						display: none;
						width: 105px;
						height: 34px;
						font-size: 14px;
						line-height: 1.5;
						font-weight: 600;
						text-transform: uppercase;
						background: #01D49E;
						color: #FFFFFF;
						border-radius: 8px;
					}
				}
			}
			&:not(.js_slide_leader) {
				@media (min-width: 576px) {
					display: flex;
					flex-wrap: wrap;
					@include gap(2,0px);
				}
				.item-slide {
					padding: 15px;
				}
			}
		}



		.group-btn-slide {
			position: absolute;
			display: block;
			top: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			left: 0;
			right: 0;
		}
		.swiper-pagination-slide-leader {
			position: relative;
			z-index: 2;
			left: 0;
			right: 0;
			margin: 10px auto 0;
			display: flex;
			justify-content: center;
			align-items: center;
			pointer-leaders: none;
			&.swiper-pagination-lock {
				display: none;
			}
			&>* {
				pointer-leaders: auto;
			}
			.swiper-pagination-bullet {				
				width: 40px;
				height: 3px;
				margin: 4px;
				background: #1169b0;
				border-radius: 0;
				border: 1px solid #1169b0;
				opacity: .7;
				box-shadow: 0 0px 1px rgba(122, 122, 122, 0.5);	
				outline: none;
			}
			.swiper-pagination-bullet-active {
				height: 7px;
				background: transparent;
				opacity: 1;
				cursor: default;
			}
		}
		&:hover {
			.swiper-button-next-slide-leader, .swiper-button-prev-slide-leader {
				background: rgba(255,255,255,.8);
				//filter: drop-shadow(0px 1px 2px #000);
				opacity: .9;
			}
		}
		.swiper-button-next-slide-leader, .swiper-button-prev-slide-leader {
			position: absolute;
			z-index: 6;
			top: 50%;
			width: 35px;
			height: 35px;
			margin-top: -35px;
			background: rgba(255,255,255,.3);
			color: #F27227;
			border-radius: 50%;
			outline: none;
			cursor: pointer;
			opacity: .7;
			box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.1);
			// box-shadow: 0 1px 5px 0 rgba(0,0,0,0.16), 0 1px 2px 0 rgba(0,0,0,0.26);
			&:after {
				content: "";
				position: absolute;
				display: block;
				top: 0;
				right: 0;
				width: 35px;
				height: 35px;
				line-height: 35px;
				text-align: center;
				vertical-align: middle;
				z-index: 2;
				font-family: 'Font Awesome 5 Pro';
				font-size: 24px;
				font-weight: 300;
				-moz-osx-font-smoothing: grayscale;
				-webkit-font-smoothing: antialiased;
				display: inline-block;
				font-style: normal;
				font-variant: normal;
				text-rendering: auto;
			}
			&.swiper-button-disabled {
				cursor: default;
				opacity: 0;
			}
		}
		.swiper-button-prev-slide-leader {
			left: -50px;
			right: auto;
			&:after {
				content: "\f177";
			}
		}
		.swiper-button-next-slide-leader {
			right: -50px;
			left: auto;
			&:after {
				content: "\f178";
			}
		}
	}
}
/* End About Lanh dao css
============================================================================================ */



/* Start Home News Area css
============================================================================================ */
.section-news-crs {
	position: relative;
	display: block;
	padding: 40px 0;
	background-color: #fff;
	.news-inner {
		display: block;
        @media (min-width: 1400px) {
            padding: 0 50px;
        }
	}
	.news-box-title {
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		z-index: 0;
		.title-name {
			display: inline-block;
			padding-right: 20px;
			margin-bottom: 10px;
		}
		.btn-viewmore {
			color: #F27227;
			&:after {
				content: "\f178";
				position: relative;
				display: inline-block;
				top: 0;
				right: 0;
				width: 35px;
				height: 35px;
				line-height: 35px;
				text-align: center;
				vertical-align: middle;
				z-index: 2;
				font-family: 'Font Awesome 5 Pro';
				font-size: 16px;
				font-weight: 500;
				-moz-osx-font-smoothing: grayscale;
				-webkit-font-smoothing: antialiased;
				font-style: normal;
				font-variant: normal;
				text-rendering: auto;
			}
		}
	}
	.title-sub {
		position: relative;
		display: block;
		width: 100%;
		font-size: 19px;
		line-height: 1.4;
		text-align: justify;
		margin-bottom: 25px;
	}
	.news-box-list {
		position: relative;
		display: block;
		vertical-align: top;
		width: 100%;
		.news-list {
			position: relative;
			display: block;
			overflow: hidden;
			margin: 0 -10px 15px;
			@media (min-width: 992px) {
				margin: 0 -30px 15px;
			}

			.news-item {
				position: relative;
				display: block;
				float: left;
				overflow: hidden;
				width: 100%;
				padding: 10px 10px;
				@media (min-width: 768px) {
					width: 60%;
				}
				@media (min-width: 992px) {
					width: 54%;
					padding: 25px 30px;
				}
				&:nth-child(1) {
					@media (min-width: 768px) {
						width: 40%;
					}
					@media (min-width: 992px) {
						width: 46%;
					}
					.item-inner {
						flex-direction: column;
						.item-image {
							width: 100%;
							padding-right: 0;
						}
						.item-info {
							width: 100%;
							.item-title {
								margin-top: 10px;
								.item-title-a {
									color: #333333;
								}
							}
						}
					}
				}
				.item-inner {
					position: relative;
					display: flex;
					flex-direction: row;
					padding: 0;
					// background: #FFFFFF;
					// box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
					@media (min-width: 768px) {
						flex-direction: row;
					}
					.item-image {
						position: relative;
						display: block;
						width: 50%;
						overflow: hidden;
						padding-right: 10px;
						@media (min-width: 992px) {
							padding-right: 20px;
						}
						.item-image-a {
							position: relative;
							display: block;
							margin: 0 auto;
						}
					}
					.item-info {
						position: relative;
						display: block;
						width: 50%;
						background: #fff;
						.item-title {
							position: relative;
							display: block;
							width: 100%;
							margin: 0 0 10px;
							padding: 0;
							.item-title-a {
								position: relative;
								display: block;
								display: -webkit-box;
								padding: 0;
								margin: 0;
								font-size: 18px;
								line-height: 1.3;
								color: #024173;
								font-weight: 600;
								text-decoration: none;
								overflow: hidden;
								-webkit-box-orient: vertical;
								text-overflow: ellipsis;
								-webkit-line-clamp: 2;
								word-break: break-word;
								@media (min-width: 992px) {
									font-size: 22px;
								}
							}
						}
						.item-description {
							display: -webkit-box;
							max-height: 42px;
							margin: 0 0 5px;
							color: #333333;
							font-size: 16px;
							line-height: 1.3;
							padding: 0;
							overflow: hidden;
							-webkit-box-orient: vertical;
							text-overflow: ellipsis;
							-webkit-line-clamp: 2;
							word-break: break-word;
						}
						.item-time-publish {
							margin: 0 0 5px;
							color: #707070;
							font-size: 14px;
						}
						.btn-readmore {
							position: relative;
							display: inline-flex;
							padding: 8px 0;
							margin: 0;
							color: #F27227;
							font-size: 16px;
							line-height: 1.3;
							font-weight: 600;
							text-decoration: none;
							&:after {
								content: "\f178";
								position: relative;
								padding-left: 8px;
								text-align: center;
								vertical-align: middle;
								z-index: 2;
								font-family: 'Font Awesome 5 Pro';
								font-size: 15px;
								-moz-osx-font-smoothing: grayscale;
								-webkit-font-smoothing: antialiased;
								font-style: normal;
								font-variant: normal;
								text-rendering: auto;
							}
						}
					}
				}
				.item-inner:hover {
				}
			}
		}

		.btn-viewmore {
			position: relative;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			min-width: 150px;
			height: 40px;
			padding: 0 12px;
			margin: 10px auto;
			color: #FFFFFF;
			background-color: #F27227;
			border: 1px solid #F27227;
			border-radius: 5px;
			font-size: 14px;
			line-height: 1.3;
			font-weight: 600;
			text-align: center;
			text-decoration: none;
		}
	}
}
