.section-mission {
	position: relative;
	display: block;
	padding: 50px 0;
	color: #fff;
	background-color: #0a0a0a;
	background-image: url('../images/background-unit.png');
	background-repeat: no-repeat;
	background-position: center;
	.unit-inner {
		display: block;
	}

	.home-box-title { 
		position: relative;
		width: 100%;
		color: #fff;
		.title-name {
			position: relative;
			display: flex;
			font-size: 20px;
			line-height: 31px;
			margin-bottom: 20px;
		}
		.title-sub {
			position: relative;
			display: block;
			width: 100%;
			height: 103px;
			font-size: 18px;
			line-height: 1.4;
			text-align: justify;
			margin-bottom: 25px;
		}
		.title-mission {
			display: flex;
			flex-direction: column;
			// transform: translateX(-50px);
			margin-bottom: 20px;
			.title-small {
				font-size: 20px;
				font-weight: 600;
				@media (max-width: 767px) {
					font-size: 16px;
				}
			}
			.title-larger {
				font-weight: 800;
				font-size: 34px;
				@media (max-width: 767px) {
					font-size: 24px;
				}
