.fptOnline {

    .back-to-top-badge {
        display: none !important;
        position: fixed;
        z-index: 7;
        right: 18px;
        bottom: 5px;
        width: 34px;
        height: 34px;
        font-size: 12px;
        line-height: 32px;
        color: #5b5b5b;
        background: rgba(255, 255, 255, 0.8);
        border: 1px solid #bfbfbf;
        border-radius: 4px;
        text-align: center;
        transform: translateY(40px);
        transition: all 250ms ease;
        cursor: pointer;
        &:hover {
            color: $border-title-highlight;
            background: #fff;
            border-color: $border-title-highlight;
        }
        i {
            height: 32px;
            width: 32px;
            text-align: center;
            font-size: 26px;
            line-height: 32px;
            pointer-events: none;
        }
        &.back-to-top-badge-visible {
            pointer-events: all;
            transform: translateY(-55px);
            transition: all 250ms ease;
        }
        &.back-to-top-small {
            overflow: hidden;
            bottom: 0;
            font-size: 0;
        }
        @media (max-width: 991px) {
            right: 12px;
        }
    }
}

/* Popup dialog area css
============================================================================================ */
.modal.popup-dialog {
    .modal-dialog {
        margin-top: 2rem;
        @media (min-width: 576px) {
            max-width: 600px;
            margin-top: 4rem;
        }
        @media (min-width: 1010px) {
            max-width: 900px;
            margin-top: 6rem;
        }
    }
    .modal-header {
        padding: .7rem 1rem;
        background: #411BFF;
        font-weight: bold;
        font-size: 20px;
        line-height: 23px;
        color: #FFFFFF;
        border-radius: 0;
        .bootbox-close-button {
            position: absolute;
            display: block;
            right: 0;
            top: 0;
            width: 32px;
            height: 32px;
            padding: 0;
            margin: 0;
            line-height: 32px;
            background-color: #FFFFFF;
            background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M1 1L13 13M13 1L1 13' stroke='%239B9B9B'/%3e%3c/svg%3e");
            background-repeat: no-repeat;
            background-position: center;
            font-size: 0;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            border: 0;
            border-radius: 50%;
            outline: none;
            opacity: 1;
            transform: translate(50%, -50%);
        }
    }
    .modal-content {
        background: #FFFFFF;
        border: 0;
        border-radius: 0px;
        .bootbox-body {
            padding: 15px 0 0;
            text-align: center;

            //Start - form register page payment area
            .wrap-frm-register {
                font-size: 14px;
                text-align: left;
                .frmRegister {
                    padding: 0 40px;
                    @media (max-width: 767px) {
                        padding: 0;
                    }
                }
                p {
                    margin-bottom: 5px;
                }
                .form-group {
                    margin-bottom: 15px;
                }
                .control-label {
                    margin-bottom: 8px;
                }
                .list-radios {
                    display: flex;
                    flex-wrap: wrap;
                    text-align: center;
                    .radio-inline {
                        &:not(:last-child) {
                            margin-right: 15px;
                        }
                    }
                }
                .radio {
                    margin-bottom: 8px;
                    &>label {
                        display: flex;
                        white-space: nowrap;
                        line-height: 1.3;
                        input {
                            margin-right: 6px;
                            margin-top: 3px;
                        }
                    }
                }
                .checkbox {
                    label {
                        position: relative;
                        padding-left: 25px;
                        input {
                            position: absolute;
                            top: 5px;
                            left: 0;
                        }
                        span {
                            display: block;
                            &.input-title {
                                font-size: 14px;
                                color: #373946;
                                font-weight: bold;
                            }
                            &.input-description {
                                display: -webkit-box;
                                font-size: 14px;
                                color: #373946;
                                font-weight: normal;
                                overflow: hidden;
                            }
                            &.btn-view-more {
                                font-size: 12px;
                                color: #2F84EC;
                                font-weight: normal;
                                z-index: 2;
                            }
                        }
                    }
                }
                .textarea {
                }
                select,
                input,
                textarea {
                    background: #F7F7F7;
                    border: 1px solid #E5E5E5;
                    border-radius: 0;
                    outline: none;
                    font-size: 14px;
                    &:focus {
                        border: 1px solid $border-title-highlight;
                        box-shadow: none;
                    }
                }
                textarea {
                    width: 100%;
                    min-height: 80px;
                    max-height: 150px;
                    padding: 10px 12px;
                    resize: vertical;
                }
            }
            //End - form register page payment area

            //Start - form register page payment area
            .wrap-frm-register-thank {
