                padding-bottom: 20px;
                background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='70' height='70' viewBox='0 0 70 70' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cg clip-path='url(%23clip0)'%3e%3cpath d='M64.1084 29.3403C62.5466 29.3403 61.2808 30.6063 61.2808 32.1679V35.0159C61.2765 42.4449 58.3795 49.4277 53.1233 54.6777C47.871 59.9239 40.8915 62.8125 33.4685 62.8125C33.463 62.8125 33.4575 62.8125 33.4521 62.8125C18.1161 62.8037 5.64649 50.3201 5.65524 34.9839C5.65948 27.555 8.55655 20.5723 13.8128 15.322C19.0652 10.0758 26.0448 7.18722 33.4677 7.18722C33.4731 7.18722 33.4786 7.18722 33.4841 7.18722C37.4041 7.1894 41.2073 7.9996 44.788 9.59497C46.2148 10.2307 47.8859 9.58937 48.5216 8.16298C49.1571 6.73659 48.5162 5.06507 47.0896 4.42933C42.7808 2.50938 38.2044 1.53458 33.4872 1.53198C33.4801 1.53198 33.4746 1.53198 33.4675 1.53198C24.5358 1.53198 16.1361 5.00847 9.81614 11.321C3.49125 17.6385 0.00520083 26.0411 5.51318e-06 34.9809C-0.00505308 43.9204 3.47116 52.327 9.78893 58.6518C16.1067 64.9767 24.5092 68.4628 33.4488 68.468C33.4558 68.468 33.4614 68.468 33.4685 68.468C42.4002 68.468 50.7999 64.9916 57.1199 58.6789C63.4447 52.3613 66.9308 43.9587 66.936 35.0175V32.1681C66.936 30.6063 65.6701 29.3403 64.1084 29.3403Z' fill='url(%23paint0_linear)'/%3e%3cpath d='M69.1717 5.44175C68.0674 4.33761 66.2771 4.33761 65.1728 5.44175L33.4678 37.1466L26.2752 29.9539C25.1711 28.8496 23.3806 28.8496 22.2765 29.9539C21.1722 31.0582 21.1722 32.8485 22.2765 33.9528L31.4686 43.145C32.0207 43.6972 32.7445 43.9732 33.4681 43.9732C34.1918 43.9732 34.9154 43.6972 35.4676 43.145L69.1717 9.44064C70.2759 8.33636 70.2759 6.54603 69.1717 5.44175Z' fill='url(%23paint1_linear)'/%3e%3c/g%3e%3cdefs%3e%3clinearGradient id='paint0_linear' x1='33.468' y1='1.53198' x2='33.468' y2='68.468' gradientUnits='userSpaceOnUse'%3e%3cstop stop-color='%231FE0A5'/%3e%3cstop offset='1' stop-color='%232F84EC'/%3e%3c/linearGradient%3e%3clinearGradient id='paint1_linear' x1='45.7241' y1='4.61365' x2=