        }
        .modal-footer {
            justify-content: center;
            border: 0;
            padding-top: 0;
            padding-bottom: 18px;
            .btn-bootbox {
                display: flex;
                flex-direction: row;
                justify-content: center;
                min-width: 89px;
                padding: 6px ​15px;
                background-color: #01D49E;
                color: #FFFFFF;
                border-radius: 3px;
                font-weight: bold;
                font-size: 16px;
                outline: none;
                box-shadow: none;
                &.btn-xanh,
                &.btn-yes,
                &.btn-confirm {
