

/* Home Banner Fluid Area css
============================================================================================ */
.banner-top {
	position: relative;
	display: block;
	width: 100%;
	max-width: 100%;
	overflow: hidden;
	.slide-home {

		.parallax-bg {
			position: absolute;
			left: 0;
			top: 0;
			width: 130%;
			height: 100%;
			background-color: #fdfffc;
			background-size: cover;
			background-position: center;
		}
		.item-slide {
			position: relative;
			display: flex;
			align-items: center;
			height: 100%;
			height: 768px;
			overflow: hidden;
			color: #fff;
			@media (max-width: 767px) {
				padding: 0 45px;
			}
			.item-slide-inner {
				position: relative;
				display: block;
				width: 100%;
				color: #fff;
				text-align: center;
			}
			.slide-background {
				position: absolute;
				display: none;
				img {
					width: 100%;
					height: auto;
					object-fit: cover;
				}
			}

			.title {
				max-width: 406px;
				margin: 0 0 15px;
				font-size: 32px;
				line-height: 1.3;
				font-weight: 600;
				color: #024173;
				clear: both;
			}
			.subtitle {
				margin: 0 0 5px;
				font-size: 18px;
				font-weight: 600;
			}
			.text {
				margin: 0 0 15px;
				font-size: 15px;
				max-width: 400px;
				line-height: 1.3;
				color: #333;
			}
			.btn-readmore {
				display: inline-flex;
				align-items: center;
				height: 32px;
				padding: 5px 15px;
				margin: 15px 0;
				font-size: 13px;
				line-height: 32px;
				font-weight: 600;
				background-color: transparent;
				border: 1px solid #024173;
				border-radius: 5px;
				color: #024173;
				backdrop-filter: none;
			}

		}
		.group-btn-slide {
			position: absolute;
			display: block;
			top: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			left: 0;
			right: 0;
		}
		.wrap-pagination {
			position: absolute;
			z-index: 2;
			bottom: 10px;
			left: 0;
			right: 0;
		}
		.swiper-pagination-slide-home {
			position: absolute;
			z-index: 2;
			bottom: 10px;
			left: 0;
			right: 0;
			margin: 0 auto;
			display: flex;
			align-items: center;
			pointer-events: none;
			&>* {
				pointer-events: auto;
			}
			.swiper-pagination-bullet {				
				width: 40px;
				height: 3px;
				margin: 4px;
				background: #fff;
				border-radius: 0;
				border: 1px solid #fff;
				opacity: .7;
				box-shadow: 0 0px 1px rgba(122, 122, 122, 0.5);	
				outline: none;
			}
			.swiper-pagination-bullet-active {
				height: 7px;
				background: transparent;
				opacity: 1;
				cursor: default;
			}
		}
		&:hover {
			.swiper-button-next-slide-home, .swiper-button-prev-slide-home {
				background: rgba(255,255,255,.8);
				//filter: drop-shadow(0px 1px 2px #000);
				opacity: .9;
			}
		}
		.swiper-button-next-slide-home, .swiper-button-prev-slide-home {
			position: absolute;
			z-index: 6;
			top: 50%;
			width: 35px;
			height: 35px;
			margin-top: -17px;
			background: rgba(255,255,255,.3);
			border-radius: 50%;
			outline: none;
			cursor: pointer;
			opacity: .7;
			box-shadow: 0 1px 5px 0 rgba(0,0,0,0.16), 0 1px 2px 0 rgba(0,0,0,0.26);
			&:after {
				content: "";
				position: absolute;
				display: block;
				top: 0;
				right: 0;
				width: 35px;
				height: 35px;
				line-height: 35px;
				text-align: center;
				vertical-align: middle;
				z-index: 2;
				font-family: 'Font Awesome 5 Pro';
				font-size: 24px;
				font-weight: 300;
				-moz-osx-font-smoothing: grayscale;
				-webkit-font-smoothing: antialiased;
				display: inline-block;
				font-style: normal;
				font-variant: normal;
				text-rendering: auto;
			}
			&.swiper-button-disabled {
				cursor: default;
				opacity: 0;
			}
		}
		.swiper-button-prev-slide-home {
			left: 10px;
			right: auto;
			&:after {
				content: "\f104";
			}
		}
		.swiper-button-next-slide-home {
			right: 10px;
			left: auto;
			&:after {
				content: "\f105";
			}
		}
	}
}

/* End Banner Fluid Area css
============================================================================================ */


.section-banner-gallery-grid-area {
	position: relative;
	display: block;
	// overflow: hidden;
	&-inner {
		position: relative;
		display: block;
		padding-bottom: 35%;
		@media (min-width: 575px) {
			padding-bottom: 5%;
		}
		@media (min-width: 768px) {
			padding-bottom: 0;
		}
		.wrap-gallery-grid {
			position: relative;
			&:before {
				content: '';
				position: absolute;
				z-index: 2;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				// background: linear-gradient(90deg, rgba(18, 104, 177, 0.3) 0.01%, rgba(22, 177, 75, 0.3) 51.41%, rgba(242, 114, 39, 0.3) 102.81%);
				background: linear-gradient(90deg, rgba(66, 70, 73, 0.5) 0.01%, rgba(33, 146, 171, 0.5) 102.81%);
				mix-blend-mode: normal;
				opacity: 0.7;
			}
			&:after {
				content: '';
				position: absolute;
				z-index: 3;
				width: 100%;
				height: 107px;
				left: 0;
				bottom: 0;
				background: linear-gradient(0deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0.9) 10%, rgba(255, 255, 255, 0) 100%);
				mix-blend-mode: normal;
				@media (min-width: 768px) {
					display: none;
				}
			}
		}
		.wrap-over-banner {
			z-index: 4;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 30px;
			@media (min-width: 370px) {
				padding: 40px;
			}
			@media (min-width: 576px) {
				padding: 50px;
			}
			.wrap-over-banner-inner {
				position: relative;
				display: flex;
				flex-direction: column;
				align-items: center;
				max-width: 957px;
				width: 100%;
				@media (min-width: 420px) {
					box-shadow: 0 0 50px rgba(0, 0, 0, 0.3);
				}
				.row-1 {
					width: 90%;
					padding: 5% 5% 10%; 
					color: #024173;
					font-size: 30px;
					font-weight: bold;
					line-height: 1.3;
					text-align: center;
					background-color: rgba(#fff,.95);
					backdrop-filter: blur(2px);
					@media (max-width: 370px) {
						font-size: 24px;
					}
					@media (min-width: 420px) {
						width: 100%;
						padding: 5% 12% 10%; 
					}
					@media (min-width: 768px) {
						padding: 5% 10% 8%; 
						font-size: 46px;
					}
					@media (min-width: 1200px) {
						padding: 8% 12% 11%; 
						font-size: 54px;
					}
					&>* {
						font-size: inherit;
					}
					font {
						white-space: nowrap;
					}
				}

				.row-2 {
					position: relative;
					width: 100%;
					@media (min-width: 420px) {
						&:before {
							content: '';
							position: absolute;
							display: block;
							top: 0;
							left: 0;
							width: 100%;
							height: 100%;
							background: linear-gradient(0deg, #505051, #505051), linear-gradient(143.07deg, rgba(0, 38, 68, 0.95) -10.07%, rgba(8, 81, 139, 0.9) 68.52%);
							opacity: 0.7;
							overflow: hidden;
							backdrop-filter: blur(2px);
							@media (min-width: 768px) {
								opacity: 0.95;
							}
						}
					}
					.list-item {
						margin-top: -20px;
						display: flex;
						flex-wrap: wrap;
						justify-content: center;
						//@include gap(1,22px);
						@include gap(1,0px);

						@media (min-width: 420px) {
							padding: 0 20px 22px;
						}
						@media (min-width: 768px) {
							flex-wrap: nowrap;
							margin-top: -50px;
							// @include gap(3,30px);
							@include gap(3,0px);
						}
						@media (min-width: 992px) {
							padding: 0 35px 37px;
						}

						$giai_phap_1_bg: #1169B0;
						$giai_phap_2_bg: #F27227;
						$giai_phap_3_bg: #16B14B;
						.item-cell {
							min-width: 200px;
							position: relative;
							display: flex;
							flex-direction: column;
							justify-content: space-between;
							align-items: stretch;
							margin: 0 0 20px;
							background: $giai_phap_1_bg;
							color: #fff;
							border: 1px solid rgba(255, 255, 255, 0.5);
							border-radius: 10px;
							@media (min-width: 768px) {
								margin: 0 15px;
							}
							&:nth-child(2) {
								background: $giai_phap_2_bg;
							}
							&:nth-child(3) {
								background: $giai_phap_3_bg;
							}
							&:last-child {
								margin-bottom: 0;
							}
							&:hover {
								box-shadow: 2px 2px 20px rgba(0, 0, 0, 0.3);
								border: 1px solid rgba(255, 255, 255, 0.9);
								transform: scale(1.08);
							}

							.item-cell-head {
								padding: 25px 22px 0; 
								margin: 0;
								font-size: 22px;
								line-height: 1.3;
								@media (max-width: 370px) {
									font-size: 18px;
								}
								@media (min-width: 768px) {
									padding: 30px 22px 5px; 
								}
								p {}
							}

							.item-cell-body {
								display: flex;
								justify-content: space-between;
								align-items: flex-end;
								flex-direction: row-reverse;
								padding: 5px 22px 10px;
								.cell-1 {
									img,svg {
										max-width: 70px;
										height: auto;
									}
								}
							}
							.btn-readmore {
								font-size: 16px;
								font-weight: 600;
								&:after {
									content: "\f178";
									position: relative;
									padding-left: 10px;
									text-align: center;
									vertical-align: middle;
									z-index: 2;
									font-family: 'Font Awesome 5 Pro';
									font-size: 16px;
									font-weight: 500;
									-moz-osx-font-smoothing: grayscale;
									-webkit-font-smoothing: antialiased;
									font-style: normal;
									font-variant: normal;
									text-rendering: auto;
								}
								@media (max-width: 370px) {
									font-size: 14px;
								}
							}
							a {
								color: inherit;
							}
						}
					}
				}
			}
		}
	}
}


/* Start Counters
============================================================================================ */
.section-number-fun-area {
	position: relative;
	display: block;
	width: 100%;
	padding: 10px 0 0;
	overflow: hidden;
	@media (min-width: 768px) {
		padding: 50px 0 0;
	}
	&-inner {
		position: relative;
		display: block;
		@media (min-width: 1400px) {
			padding: 0 50px;
		}
		.number-counters {
			position: relative;
			.counters-item {
				text-align: center;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				margin-bottom: 20px;
				.item-icon {
					display: flex;
					justify-content: center;
					align-items: center;
					margin-bottom: 10px;
					width: 80px;
					height: 80px;
					svg {
						max-width: 100%;
						max-height: 100%;
					}
				}
				.item-value {
					display: flex;
					justify-content: center;
					align-items: center;
					margin-bottom: 5px;
					font-size: 35px;
					line-height: 1.4;
					font-weight: bold;
					white-space: nowrap;
					@media (max-width: 370px) {
						font-size: 24px;
					}
					@media (min-width: 768px) {
						font-size: 41px;
					}
				}
				.item-label {
					display: flex;
					justify-content: center;
					font-size: 16px;
					line-height: 1.3;
				}
				i {
					color: #4fb8aa;
					display: block;
					font-size: 41px;
					margin-bottom: 10px;
				}
			}
		}
	}
}
/* End Counters
============================================================================================ */


