/* Home slide-coverflow Area css - Box Giai phap
============================================================================================ */
.section-slide-coverflow-area { 
	position: relative;
	display: block;
	padding: 40px 0;
	width: 100%;
	overflow: hidden;
	background-color: #fff;
	.section-box-title { 
		position: relative;
		display: block;
		width: 100%;
		text-align: center;
		.title-name { 
			font {
			}
		}
		.title-sub { 
		}
	}

	.section-box-content { 
		position: relative;
		display: block;
		width: 100%;
		.js_parent_slide { 
			position: relative;
			overflow: hidden;
			--slide-gutter-x: 1.5rem;
			margin-right: calc(var(--slide-gutter-x) / -2);
			margin-left: calc(var(--slide-gutter-x) / -2);
			@media (min-width: 768px) {
			}
			.slide-list-item { 
				position: relative;
				width: 100%;
				.swiper-slide {
					// opacity: 0.7;
					&.swiper-slide-active {
						// opacity: 1;
						.slide-item .item-inner {
							&:after {
								opacity: 0;
								pointer-events: none;
							}
							.item-info {
								.info-bottom {
									display: flex;
									flex-direction: column;
									@media (min-width: 768px) {
										flex-direction: row;
									}
								}
								.item-title {
									justify-content: unset;
								}
							}
						}
					}
					.slide-item .item-inner  {
						&:after {
							content: '';
							position: absolute;
							width: 100%;
							height: 100%;
							top: 0;
							left: 0;
							background-color: #fff;
							opacity: 0.3;
							animation-name: fadeIn;
							transition: 1.3s;
						}
					}
				}
				.slide-item { 
					position: relative;
					display: block;
					width: 100%;
					max-width: 768px;
					padding: 5px;
					margin: 0;
					.item-inner { 
						position: relative;
						display: block;
						padding: 0;
						// background: #FFFFFF;
						// box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
						.item-image {
							position: relative;
							display: block;
							width: 100%;
							overflow: hidden;
							.item-image-a {
								position: relative;
								display: block;
								margin: 0 auto;
							}
						}

						.item-info { 
							position: relative;
							display: block;
							width: 100%;
							background: #1169B0;
							color: #fff;
							padding: 10px 15px 20px 20px;
							border-radius: 0 0 5px 5px;
							overflow: hidden;
							@media (min-width: 768px) {
								padding: 10px 27px 20px 35px;
							}
							.info-bottom { 
								display: flex;
								justify-content: space-between;
								transition: 2s;
								@media (min-width: 768px) {
									display: none;
								}
							}
							.item-title {
								position: relative;
								display: flex;
								justify-content: center;
								width: 100%;
								margin: 0;
								padding: 10px 0;
								.item-title-a {
									position: relative;
									display: block;
									display: -webkit-box;
									padding: 0;
									margin: 0;
									font-size: 24px;
									line-height: 1.3;
									color: inherit;
									font-weight: 600;
									text-decoration: none;
									overflow: hidden;
									-webkit-box-orient: vertical;
									text-overflow: ellipsis;
									-webkit-line-clamp: 2;
									word-break: break-word;
								}
							}
							.item-description {
								max-height: 72px;
								margin: 0 0 5px;
								color: inherit;
								font-size: 16px;
								padding: 0;
								overflow: hidden;
								-webkit-box-orient: vertical;
								text-overflow: ellipsis;
								-webkit-line-clamp: 3;
								word-break: break-word;
								@media (min-width: 768px) {
									padding-right: 15px;
								}
							}
							.item-time-publish {
								color: #707070;
								font-size: 14px;
							}

							.btn-viewmore { 
								position: relative;
								display: inline-flex;
								align-items: center;
								justify-content: center;
								min-width: 150px;
								height: 40px;
								padding: 0 6px 0 12px;
								margin: 10px auto;
								color: #FFFFFF;
								background-color: #F27227;
								border: 1px solid #F27227;
								border-radius: 5px;
								font-size: 14px;
								line-height: 1.3;
								font-weight: 600;
								text-align: center;
								text-decoration: none;
								cursor: pointer;
								&:after {
									content: "\f178";
									position: relative;
									padding-left: 10px;
									text-align: center;
									vertical-align: middle;
									z-index: 2;
									font-family: 'Font Awesome 5 Pro';
									font-size: 16px;
									font-weight: 500;
									-moz-osx-font-smoothing: grayscale;
									-webkit-font-smoothing: antialiased;
									font-style: normal;
									font-variant: normal;
									text-rendering: auto;
								}
							}
						}
					}
				}
			}
		}
	}
}
