.section-event-present-fuild {
	position: relative;
	display: block;
	padding: 30px 0 0;
	background-image: url('../images/bg_giai_phap_effect.png');
	background-size: auto;
	background-position: bottom;
	background-repeat: no-repeat;
	@media (max-width: 767px) {
		padding: 15px 0 0;
	}
	.section-inner {
		padding: 30px 0;
		.bg_rounded {
			position: absolute;
			top: 0;
			left: 0;
			width: 90%;
			height: 545px;
			background: linear-gradient(180deg, #EDEEF0 0%, #FFFFFF 36.77%);
			border-radius: 0 272.5px 0 0;
			z-index: -1;
			.effect {
				
			}
		}
		&:before {
			content: '';
		}
		&:after {
			content: '';
		}
	}
	.section-box-title {
		position: relative;
		display: flex;
		flex-direction: column;
		width: 100%;
		color: #454545;
		.title-name {
			position: relative;
			display: flex;
			flex-direction: column;
			font-size: 20px;
			line-height: 31px;
			margin-bottom: 20px;
			.title-small {
				color: #9F224E;
				font-size: 20px;
				font-weight: 600;
				@media (max-width: 767px) {
					font-size: 16px;
				}
			}
			.title-larger {
				font-weight: 800;
				font-size: 34px;
				@media (max-width: 767px) {
					font-size: 24px;
				}
			}
		}
		.title-sub {
			position: relative;
			display: block;
			width: 100%;
			height: auto;
			font-size: 16px;
			line-height: 1.4;
			margin-bottom: 25px;
		}
	}
	.section-list-items {
		width: 100%;
		.item-top {
			position: relative;
			display: flex;
			align-items: center;
			width: 100%;
			margin-bottom: 25px;
			@media (max-width: 991px) {
				flex-direction: column;
			}
			.item-image {
				width: 100%;
				overflow: hidden;
				text-align: right;
				padding-right: 15px;
				>a {
					position: relative;
					display: inline-block;
					max-width: 489px;
					max-height: 304px;
					overflow: hidden;
				}
			}
			.item-info {
				flex: none;
				display: flex;
				align-items: center;
				width: 50%;
				&-inner {
					position: relative;
					flex: none;
					width: 100%;
					padding: 30px 10px;
					margin: auto;
					z-index: 2;
				}
				.info-name {
					font-size: 22px;
					font-weight: bold;
					line-height: 30px;
					margin-bottom: 20px;
					@media (max-width: 991px) {
						font-size: 18px;
					}
				}
				.info-description {
					font-size: 14px;
					color: #454545;
					line-height: 1.4;
					margin-bottom: 20px;
				}
				.info-datetime {
					margin-bottom: 5px;
					font-size: 22px;
					color: #01D49E;
					@media (max-width: 991px) {
						font-size: 18px;
					}
				}
			}
			.btn-readmore {
				position: relative;
				display: inline-flex;
				align-items: center;
				color: #F27227;
				background: transparent;
				font-size: 14px;
				font-weight: 600;
				border: 1px solid #F27227;
				border-radius: 5px;
				outline: none;
				&:after {
					content: "\f178";
					position: relative;
					display: inline-block;
					top: 0;
					right: 0;
					width: 35px;
					height: 35px;
					line-height: 35px;
					text-align: center;
					vertical-align: middle;
					z-index: 2;
					font-family: 'Font Awesome 5 Pro';
					font-size: 16px;
					-moz-osx-font-smoothing: grayscale;
					-webkit-font-smoothing: antialiased;
					font-style: normal;
					font-variant: normal;
					text-rendering: auto;
				}
			}
		}
		.wrap-slide {
			position: relative;
		}
		.list-item-slide {
			position: relative;
			overflow: hidden;
			.item-slide {
				position: relative;
				display: block;
				min-height: 310px;
				.item-image {
					margin: 0 0 10px;
					overflow: hidden;
					border-radius: 8px;
				}
				.item-info {
					padding: 0 10px;
					transition: all 0.2s ease-in-out;
					.info-name {
						margin-bottom: 5px;
						font-size: 24px;
						font-weight: bold;
						line-height: 1.3;
						color: #333333;
						@media (max-width: 991px) {
							font-size: 18px;
						}
						a {
							color: inherit;
							&:hover {
								color: inherit;
							}
						}
					}
					.info-description {
						display: none;
						font-size: 14px;
						line-height: 1.6;
						color: #FFFFFF;
						letter-spacing: 0.8px;
						-webkit-box-orient: vertical;
						text-overflow: ellipsis;
						-webkit-line-clamp: 3;
						word-break: break-word;
						overflow: hidden;
					}
					.info-datetime {
						font-size: 14px;
						color: #000000;
