						height: 34px;
						font-size: 14px;
						line-height: 1.5;
						font-weight: 600;
						text-transform: uppercase;
						background: #01D49E;
						color: #FFFFFF;
						border-radius: 8px;
					}
				}
			}
		}



		.group-btn-slide {
			position: absolute;
