.fptOnline {


	/*===================================*
	  10.START BLOG STYLE
	*===================================*/
	.wrapper-detail-sidebar { 
		position: relative;
		display: block;
		width: 100%;
		padding-top: 20px;
		padding-bottom: 20px;
		@media (min-width: 992px) {
			padding-top: 60px;
			padding-bottom: 30px;
		}
		.wrapper-content { 
			display: block;
			display: flex;
			flex-direction: column;
			@media (min-width: 992px) {
				flex-direction: row;
			}
			@media (min-width: 1400px) {
				padding: 0 50px;
			}
			.area-primary { 
				width: 100%;
				@media (min-width: 992px) {
					width: percentage(8.5/12);
				}
			}

			.area-sidebar-left { 
				width: 100%;
				margin-bottom: 30px;
				@media (min-width: 992px) {
					width: percentage(3.5/12);
					padding-right: 30px;
					margin-bottom: 0;
				}
				@media (min-width: 1200px) {
					padding-right: 50px;
				}
				&-inner {
					background: #FFFFFF;
					&.box-sticky {
						position: sticky;
						top: 50px;
						@media (min-width: 992px) {
							top: 70px;
						}
					}
				}
			}
			.area-sidebar-right { 
				width: 100%;
				margin-top: 30px;
				@media (min-width: 992px) {
					width: percentage(3.5/12);
					padding-left: 30px;
					margin-top: 0;
				}
				@media (min-width: 1200px) {
					padding-left: 50px;
				}
				&-inner {
					padding: 20px;
					background: #FFFFFF;
					box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
					&.box-sticky {
						position: sticky;
						top: 50px;
						@media (min-width: 992px) {
							top: 60px;
						}
					}
				}
				.sidebar-title {
					position: relative;
					display: block;
					margin-bottom: 15px;
					padding: 0 0 5px;
					font-size: 16px;
					line-height: 1.3;
					font-weight: bold;
					color: #0e3555;
					border-bottom: 2px solid #ccc;

					display: none;
					&:before {
						content: '';
						position: absolute;
						display: block;
						width: 65px;
						height: 2px;
						background: #2aa9e0;
						bottom: -2px;
					}
					.title {

					}
				}
			}
		}

	}

	/*===================================*
	  10.END BLOG STYLE
	*===================================*/

	/* Start Tin tuc detail Area css
	============================================================================================ */
	.page-detail {
		.single-post {
			.title-name {
				margin: 0 0 10px;
				font-size: 21px;
				line-height: 1.3;
				font-weight: 600;
				@media (min-width: 767px) {
					font-size: 32px;
				}
			}

			.blog-meta {
				display: inline-block;
				padding: 0;
				margin: 0;
				width: 100%;
				list-style: none;
				font-size: 14px;
				color: #707070;
				li {
					float: left;
					margin-right: 15px;
					&:last-child {
						margin-right: 0;
					}
					a {
						display: flex;
						align-items: center;
						text-transform: capitalize;
						color: inherit;
						i {
							margin-right: 5px;
							vertical-align: middle;
							color: inherit;
						}
					}
				}
			}
			.group-act-share {
				position: relative;
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				width: 100%;
				margin-bottom: 10px;
				&>span {
					font-weight: bold;
					margin-right: 15px;
					color: #707070;
				}
				.list_sc_icons {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					padding: 10px;
					margin: 0;
					li {
						display: inline-block;
						padding: 0px 5px 5px 0;
						a {
							font-size: 18px;
							color: #fff;
							height: 34px;
							width: 34px;
							line-height: 34px;
							display: inline-block;
							text-align: center;
							vertical-align: middle;
							overflow: hidden;
						}
					}
				}
				.sc_facebook {
					background-color: #3b5998 !important;
					border-color: #3b5998 !important;
				}
				.sc_twitter {
					background-color: #00acee !important;
					border-color: #00acee !important;
				}
			}
		}

		.btn-viewhere {
			position: relative;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			min-width: 150px;
			height: 40px;
			padding: 0 6px 0 12px;
			margin: 10px auto 30px;
			color: #F27227;
			background-color: transparent;
			border: 1px solid #F27227;
			border-radius: 5px;
			font-size: 14px;
			line-height: 1.3;
			font-weight: 600;
			text-align: center;
			text-decoration: none;
			cursor: pointer;
		}

		.content-detail {
			position: relative;
			display: block;
			width: 100%;
			padding: 10px 0 5px;
			overflow: hidden;
			font-weight: normal;
			font-size: 16px;
			line-height: 1.5;
			color: #3C3241;
			&.js-content {
				&.btn-show-more-true {
					.btn-show-more {
						display: block;
					}
				}
				.group-btn-click {
					position: unset;
					display: block;
					width: 100%;
					overflow: hidden;
				}
				.btn-show-more {
					position: absolute;
					display: block;
					bottom: 0;
					left: 0;
					right: 0;
					width: 100%;
					height: 60px;
					line-height: 60px;
					padding: 0;
					margin: 0 auto;
					cursor: pointer;
					color: #00ae72;
					text-align: center;
					z-index: 3;
					background: #fff;
					&:after {
						height: 40px;
						content: "";
						background-image: linear-gradient(to top, 
							#ffffff, 
							rgba(255, 255, 255, 0.97), 
							rgba(255, 255, 255, 0.9), 
							rgba(255, 255, 255, 0.8), 

							rgba(255, 255, 255, 0.7), 
							rgba(255, 255, 255, 0.6), 
							rgba(255, 255, 255, 0.5), 
							rgba(255, 255, 255, 0.4),
							rgba(255, 255, 255, 0.3), 

							rgba(255, 255, 255, 0.2), 
							rgba(255, 255, 255, 0.1), 
							rgba(255, 255, 255, 0.05), 
							rgba(255, 255, 255, 0));
						display: block;
						position: absolute;
						bottom: 100%;
						width: 100%;
					}
					.style-btn {
						position: relative;
						display: block;
						width: 260px;
						margin: 10px auto;
						height: 36px;
						line-height: 36px;
						border: 1px solid #e4e4e4;
						border-radius: 6px;
					}
				}
				.btn-hide-more {
					position: absolute;
					display: none;
					bottom: 0;
					left: 0;
					right: 0;
					width: 200px;
					height: 36px;
					line-height: 36px;
					padding: 0 5px;
					margin: 0 auto;
					cursor: pointer;
					color: #555;
					text-align: center;
					z-index: 3;
					background: #F2F5F8;
					border: 1px solid #e4e4e4;
					border-radius: 4px;
				}
				&.active {
					padding: 15px 0 50px;
					box-sizing: unset;
					.btn-show-more {
						display: none;
					}
					.btn-hide-more {
						display: block;
					}
				}
			}

			.label {
				color: #666;
				font-weight: bold;
			}
			.lable {
				color: #666;
			}
			label {
				color: #666;
				font-weight: unset;
				font-size: 14px;
				padding: 0;
				margin-bottom: 0;
			}
			p {
				margin-bottom: 3px;
			}
			img {
				display: block !important;
				max-width: 100%;
				margin: 10px auto;
				height: auto !important;
			}
			table {
				width: 100% !important;
				max-width: 100%;
				tr {
					td {
						padding: 8px 5px;
					}
				}
			}
			iframe {
				max-width: 100% !important;
			}

			.table {
				tr {
					td {
						border: 0;
						padding: 8px 0;
					}
				}
			}
			ul {
				padding-left: 17px;
			}

		}
	}
	/* End Tin tuc detail Area css
	============================================================================================ */




	.section-news-list {
		position: relative;
		display: block;
		padding-top: 30px;
		padding-bottom: 30px;
		@media (min-width: 992px) {
			padding-top: 60px;
			padding-bottom: 40px;
		}
		.news-inner {
			display: block;
			display: flex;
			flex-direction: column;
			@media (min-width: 992px) {
				flex-direction: row;
			}
			@media (min-width: 1400px) {
				padding: 0 50px;
			}
			.area-primary { 
				width: 100%;
				@media (min-width: 992px) {
					width: percentage(8.5/12);
				}
			}


			.area-sidebar-left { 
				width: 100%;
				margin-bottom: 30px;
				@media (min-width: 992px) {
					width: percentage(3.5/12);
					padding-right: 30px;
					margin-bottom: 0;
				}
				@media (min-width: 1200px) {
					padding-right: 50px;
				}
				&-inner {
					background: #FFFFFF;
					&.box-sticky {
						position: sticky;
						top: 50px;
						@media (min-width: 992px) {
							top: 70px;
						}
					}
				}
			}
			.area-sidebar-right {
				width: 100%;
				margin-top: 30px;
				@media (min-width: 992px) {
					width: percentage(3.5/12);
					padding-left: 30px;
					margin-top: 0;
				}
				@media (min-width: 1200px) {
					padding-left: 50px;
				}
				&-inner {
					background: #FFFFFF;
					&.box-sticky {
						position: sticky;
						top: 50px;
						@media (min-width: 992px) {
							top: 60px;
						}
					}
				}
			}
		}

		.news-box-title {
			position: relative;
			display: block;
			width: 100%;
			vertical-align: top;
			color: #333;
			text-align: center;
			.title-name {
				position: relative;
				display: block;
				font-size: 24px;
				font-weight: 700;
				line-height: 29px;
				margin-bottom: 20px;
				padding: 0 0 25px;
				text-transform: uppercase;
				&:before {
					content: "";
					position: absolute;
					width: 175px;
					height: 15px;
					background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='176' height='15' viewBox='0 0 176 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cg clip-path='url(%23clip0)'%3e%3crect x='0.928925' y='6.57104' width='70' height='1' fill='%232575FC'/%3e%3crect x='88' width='10' height='10' transform='rotate(45 88 0)' fill='%232575FC'/%3e%3crect x='105.071' y='6.57104' width='70' height='1' fill='%232575FC'/%3e%3c/g%3e%3cdefs%3e%3cclipPath id='clip0'%3e%3crect x='0.928925' width='174.142' height='14.1421' fill='white'/%3e%3c/clipPath%3e%3c/defs%3e%3c/svg%3e ");
					bottom: 0;
					left: 0;
					right: 0;
					margin: 0 auto;
					pointer-events: none;
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
			}
		}
		.news-box-list {
			.news-list {
				position: relative;
				display: block;
				overflow: hidden;
				.news-item {
					position: relative;
					display: block;
					clear: both;
					width: 100%;
					padding: 15px 0;
					margin: 0;
					border-bottom: 1px solid #DEDEDE;
					overflow: hidden;
					&:nth-child(1) {
						padding-top: 0;
					}
					&:last-child {
						border-bottom: 0;
					}
					@media (min-width: 576px) {
						padding: 25px 0;
					}
					.item-inner {
						position: relative;
						display: flex;
						flex-direction: column;
						padding: 0;
						// background: #FFFFFF;
						// box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
						@media (min-width: 576px) {
							flex-direction: row;
							align-items: center;
						}
						.item-image {
							flex: none;
							position: relative;
							display: block;
							overflow: hidden;
							width: 100%;
							margin-bottom: 15px;
							@media (min-width: 576px) {
								width: percentage(4/10);
								margin-bottom: 0;
								padding-right: 15px;
							}
							@media (min-width: 992px) {
								padding-right: 40px;
							}
							.item-image-a {
								position: relative;
								display: block;
								height: 180px;
								max-height: 180px;
								margin: 0 auto;
								overflow: hidden;
								@media (max-width: 575px) {
									min-height: 170px;
									img {
										width: 100%;
										height: 100%;
										object-fit: cover;
										object-position: center;
									}
								}
								img {
									object-position: center;
								}
							}
						}
						.item-info {
							position: relative;
							display: block;
							background: #fff;
							width: 100%;
							padding: 0;
							@media (min-width: 576px) {
								min-width: percentage(6/10);
							}
							.item-title {
								position: relative;
								display: block;
								width: 100%;
								margin: 0 0 5px;
								padding: 0;
								.item-title-a {
									position: relative;
									display: block;
									display: -webkit-box;
									height: auto;
									padding: 0;
									margin: 0;
									font-weight: 600;
									font-size: 18px;
									line-height: 1.3;
									color: #333333;
									text-decoration: none;
									overflow: hidden;
									@media (min-width: 1200px) {
										font-size: 22px;
									}
								}
							}
							.item-description {
								display: -webkit-box;
								margin: 0 0 6px;
								color: #333333;
								font-size: 14px;
								padding: 0;
								overflow: hidden;
								-webkit-box-orient: vertical;
								text-overflow: ellipsis;
								-webkit-line-clamp: 3;
								word-break: break-word;
								@media (min-width: 1200px) {
									max-height: 72px;
									font-size: 16px;
									margin: 0 0 10px;
								}
							}
							.publish-time {
								font-size: 14px;
							}
						}
					}

					&.item-top {
						.item-inner {
							align-items: stretch;
							.item-image {
								margin-bottom: 0;
								@media (min-width: 576px) {
									width: percentage(5/10);
									padding-right: 0;
								}
								.item-image-a {
									max-height: none;
									height: 100%;
								}
							}
							.item-info {
								padding: 15px 15px 15px 20px;
								background-color: rgba(#C4C4C4,0.1);
								@media (min-width: 576px) {
									width: percentage(5/10);
									min-width: 50%;
								}
							}
						}
					}
				}
			}

			.line-dot {
				width: 100%;
				height: 0;
				margin-top: 28px;
				margin-bottom: 28px;
				border-bottom: 1px dashed #B4B4B4;
			}
			.btn-viewmore {
				position: relative;
				display: block;
				width: 200px;
				height: 50px;
				line-height: 50px;
				margin: 20px auto;
				color: #ffffff;
				background: transparent;
				border: 1px solid #ffffff;
				border-radius: 5px;
				font-size: 18px;
				text-align: center;
				text-decoration: none;
			}
		}

	}

	/*===================================*
	  10.START BLOG LIST STYLE
	*===================================*/
	.blog-post-item {
		position: relative;
		display: flex;
		width: 100%;
		margin-bottom: 25px;
		color: #0f0000;
		font-size: 12px;
		@media (max-width: 575px) {
			flex-direction: column;
		}
		.blog-image {
			position: relative;
			display: block;
			width: 355px;
			overflow: hidden;
			@media (max-width: 767px) {
				width: 300px;
			}
			@media (max-width: 575px) {
				width: 100%;
				margin-bottom: 15px;
			}
			a {
				position: relative;
				display: block;
				width: 100%;
				img {
					width: 100%;
					max-height: 300px;
					transition: all 0.5s ease-in-out;
					object-fit: cover;
				}
				&:hover {
					img {
						-webkit-transform: scale(1.2);
						-ms-transform: scale(1.2);
						transform: scale(1.2);
					}
				}
			}
		}
		.blog-content {
			width: calc(100% - 355px);
			padding-left: 30px;
			@media (max-width: 767px) {
				width: calc(100% - 300px);
				padding-left: 15px;
			}
			@media (max-width: 575px) {
				width: 100%;
				padding-left: 0;
			}
			&-inner {
				position: relative;
				display: block;
				width: 100%;
			}
			.blog-title {
				margin-bottom: 15px;
				font-size: 14px;
				font-weight: 700;
				line-height: 1.4;
				a {
					color: inherit;
				}
			}
			.blog-meta {
				display: inline-block;
				padding: 0;
				margin: 0 0 5px;
				width: 100%;
				list-style: none;
				li {
					float: left;
					margin-right: 15px;
					a {
						display: -webkit-box;
						display: -webkit-flex;
						display: -ms-flexbox;
						display: flex;
						-webkit-box-align: center;
						-webkit-align-items: center;
						-ms-flex-align: center;
						align-items: center;
						text-transform: capitalize;
						color: #6c6565;
						i {
							margin-right: 5px;
							vertical-align: middle;
							color: inherit;
						}
					}
				}
			}
			.discription {
				margin-bottom: 15px;
			}
			.btn-view-detail {
				font-weight: bold;
				color: #ca383d;
				text-transform: uppercase;
			}
		}
	}
	/*===================================*
	  10.End BLOG LIST STYLE
	*===================================*/


	/*===================================*
	  26.START sidebar
	*===================================*/
	.sticky {
		position: sticky;
		top: 0;
		left: 0;
		background: #fff;
		z-index: 3;
	}
	.sidebar {
		.widget {
			ul {
				padding: 0;
				list-style: none;
			}
			.widget_categories {
				li.item-menu {
					padding: 10px 20px;
					list-style: none;
					border-bottom: 1px solid #d7d7d7;
					&:last-child { 
						border-bottom: 0;
					}
					a.link {
						position: relative;
						display: block;
						width: 100%;
						padding: 8px 0;
						font-weight: 500;
						line-height: 1.4;
						color: var(--primary-2);
						&::after {
							content: "\f105";
							position: absolute;
							right: 0;
							top: 0;
							bottom: 0;
							margin: auto;
							height: 14px;
							font-family: "Font Awesome 5 Pro";
							font-size: 24px;
							line-height: 14px;
							font-weight: 300;
							color: var(--primary-1);
							user-select: none;
						}
					}
					&.active {
						border-bottom: 1px solid var(--primary-1);
						a.link {
							color: var(--primary-1);
						}
					}
				}
				.categories_num,
				.archive_num {
					float: right;
					font-size: 14px;
				}
			}
		}
	}
	/*===================================*
	  10.END sidebar
	*===================================*/

}