

.ads-fluid {
	position: relative;
	display: block;
	width: 100%;
	padding: 10px 0;
	background-color: #FFFFFF;
	clear: both;
	.ads-fluid-inner {
		position: relative;
		display: block;
		display: flex;
		width: 100%;
		justify-content: center;
		text-align: center;
		overflow: hidden;
		iframe {
			max-width: 100%;
			margin: 0 auto;
		}
		img {
			max-width: 100%;
			margin: 0 auto;
		}
	}
}


/* Footer Area css
============================================================================================ */
footer.footer_area {
	position: relative;
	display: block;
	padding: 30px 0 0;
	background-color: #0E304A;
	background-image: url('../images/bg_footer.png');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	color: #FCFBFB;
	border-top: 5px solid #F27227;
	// &:before {
	// 	content: '';
	// 	position: absolute;
	// 	top: 0;
	// 	left: 0;
	// 	width: 100%;
	// 	height: 100%;
	// 	background-color: #0E304A;
	// 	opacity: .3;
	// }
	@media (max-width: 575px) {
		background-image: none;
	}
	.container {
		@media (min-width: 1400px) {
			padding: 0 50px;
		}
	}
	.footer-inner {
		position: relative;
	}

	.footer-middle {
		position: relative;
		padding: 0;
		.logo-footer {
			display: inline-block;
			margin-bottom: 25px;
		}
		.footer-info {
			.info-title {
				margin-bottom: 10px;
				font-size: 18px;
				font-weight: 800;
				@media (max-width: 767px) {
					font-size: 18px;
				}
			}
			.info-item {
				display: flex;
				align-items: center;
				margin-bottom: 5px;
				.info-icon {
					flex: none;
					width: 21px;
					color: #DDDDDD;
					margin-right: 10px;
				}
			}
		}
		.widget {
			margin-top: 10px;
			margin-bottom: 20px;
			.widget_title {
				text-transform: capitalize;
				margin-top: 10px;
				margin-bottom: 25px;
				font-size: 18px;
			}
			.widget_links {
				list-style: none;
				padding: 0;
				li {
					list-style: none;
					padding-bottom: 12px;
					&:last-child {
						padding-bottom: 0;
					}
					a {
						font-size: 15px;
						color: #FCFBFB;
					}
				}
			}
		}

	}
	.list-socail-ft {
		max-width: 545px;
		display: flex;
		list-style: none;
		padding: 0;
		margin: 0 auto;
		font-size: 21px;
		li {
			margin-right: 15px;
			margin-bottom: 15px;
			@media (min-width: 768px) {
				margin-bottom: 8px;
			}
			a {
				display: block;
				width: 100%;
				height: 34px;
				padding: 0;
				line-height: 34px;
				white-space: nowrap;
				color: #FCFBFB;
				opacity: 50%;
				&:hover {
					opacity: 1;
					//background-image: linear-gradient(to top, #9a9a9a, #b2b2b2, #cbcbcb, #e5e5e5, #ffffff);
					i {
						&.fa-facebook-square {
							color: #2d88ff;
						}
						&.fa-twitter-square {
							color: #1da1f2;
						}
						&.fa-instagram {
							color: #d82b7e;
						}
						&.fa-youtube-square {
							color: #ff0000;
						}
					}
				}
			}
		}
	}
	.footer-bottom {
		position: relative;
		font-size: 12px;
		@media (min-width: 1199px) {
			margin-top: -25px;
		}
		p {
			a {
				color: $baseColor;
			}
		}
		.copyright {
			display: flex;
			flex-direction: column;
			justify-content: center;
			height: 100%;
			p {
				margin: 0;
			}
		}
	}
	.copy_right_text{
		text-align: center;
		margin-top: 60px;
	}

	.wrap-btn {
		position: relative;
		display: flex;
		justify-content: flex-end;
		.btn-go-top {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			z-index: 2;
			right: 0;
			top: 10px;
			width: 34px;
			height: 34px;
			font-size: 24px;
			color: #ffffff;
			background: transparent;
			border: 1px solid rgba(#FFFFFF,0.8);
			border-radius: 50%;
			-webkit-transition: all 250ms ease;
			transition: all 250ms ease;
			cursor: pointer;
			&:before {
				content: "\f106";
				font-family: "Font Awesome 5 Pro";
				font-weight: 300;
				-webkit-font-smoothing: antialiased;
				display: inline-block;
			}
			@media (max-width: 575px) {
				position: absolute;
				top: -65px;
			}
		}
	}
}
/* End Footer Area css
============================================================================================ */

$btn_size: 50px;
$btn_size_mb: 40px;
$btn_radius: 10px;

.group-action-control-bottom {
	position: fixed;
	display: block;
	bottom: 100px;
	right: 30px;
	width: $btn_size;
	height: auto;
	z-index: 8;
	color: #000000;
	font-family: inherit;
	user-select: none;
	a {
		color: #000000;
	}
	.btn-control {
		position: relative;
		display: block;
		width: $btn_size;
		height: $btn_size;
		margin-bottom: 10px;
		background-color: #C4C4C4;
		color: #000000;
		font-weight: bold;
		font-size: 12px;
		text-align: center;
		text-transform: uppercase;
		border-radius: $btn_radius;
		line-height: 75px;
		overflow: hidden;
		cursor: pointer;
		&:last-child {
			margin-bottom: 0;
		}
	}
	.btn-go-top {
		background-image: $url_bg_btn_go_top;
		background-position: top 7px center;
		background-repeat: no-repeat;
		background-size: 45%;
		display: none;
		&.show {
			display: block;
		}
	}
	.btn-chat-messege {
		background: transparent;
		line-height: 100%;
		z-index: 2;
		&:before {
			content: "";
			position: absolute;
			display: block;
			width: 40px;
			height: 25px;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			margin: auto;
			background-color: $bg_trang;
			border-radius: 50%;
			overflow: hidden;
		}
		.icon-messeger {
			position: relative;
			display: block;
			width: 100%;
			height: 100%;
			background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='80' height='81' viewBox='0 0 80 81' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M0 37.0382C0 48.692 5.81358 59.0891 14.904 65.8794V80.0033L28.5245 72.5297C32.158 73.5331 36.0082 74.0798 39.9983 74.0798C62.0893 74.0798 80 57.4991 80 37.0415C80.0033 16.584 62.0926 0 40.0017 0C17.9107 0 0 16.5807 0 37.0382H0ZM35.7815 26.6644L46.2153 37.5282L65.8394 26.6644L43.9718 49.8721L33.7881 39.0116L13.9106 49.8754L35.7815 26.6644Z' fill='%232196F3'/%3e%3c/svg%3e");
			background-repeat: no-repeat;
			background-position: center;
			background-size: 90%;
		}
	}
	.btn-go-home {
		background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='32' height='31' viewBox='0 0 32 31' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M31.4625 13.513L17.1052 0.430717C16.475 -0.143593 15.5247 -0.143531 14.8948 0.430655L0.537376 13.5131C0.0325651 13.9731 -0.134184 14.6819 0.11244 15.3187C0.359126 15.9555 0.959749 16.3669 1.64268 16.3669H3.9358V29.4744C3.9358 29.9941 4.35718 30.4155 4.87686 30.4155H12.7465C13.2661 30.4155 13.6875 29.9942 13.6875 29.4744V21.516H18.3126V29.4745C18.3126 29.9942 18.734 30.4156 19.2537 30.4156H27.1229C27.6426 30.4156 28.064 29.9942 28.064 29.4745V16.3669H30.3575C31.0404 16.3669 31.6411 15.9554 31.8878 15.3187C32.1341 14.6818 31.9673 13.9731 31.4625 13.513Z' fill='white'/%3e%3c/svg%3e ");
		background-position: top 7px center;
		background-repeat: no-repeat;
		background-size: 35%;
	}
	.btn-go-top,
	.btn-go-home {
		&:hover {
			background-color: #bbbbbb;
			box-shadow: 0px 2px 10px 0px rgba(0,0,0,0.08);
		}
	}
	@media (max-width: 767px) {
		width: $btn_size_mb;
		bottom: 100px;
		right: 10px;
		.btn-control {
			width: $btn_size_mb;
			height: $btn_size_mb;
			font-size: 12px;
			line-height: 60px;
			border-radius: 6px;
		}
		.btn-go-top {
			background-size: 50%;
			background-position: top 5px center;
		}
		.btn-chat-messege {
			&:before {
				width: 25px;
				height: 15px;
			}
			.icon-messeger {
				background-size: 80%;
			}
		}
		.btn-go-home {
			background-size: 40%;
			background-position: top 5px center;
		}
	}
}

$btn2_size: 50px;
$btn2_size_mb: 50px;
.group-action-control-right {
	position: fixed;
	display: none;
	bottom: 25%;
	right: -80px;
	width: auto;
	height: 50px;
	z-index: 12;
	color: #1169B0;
	font-family: inherit;
	user-select: none;
	@media (min-width: 1200px) {
		display: block;
	}
	.btn-control {
		position: relative;
		display: block;
		width: 132px;
		height: $btn2_size;
		margin-bottom: 0;
		background: #FFFFFF;
		color: #1169B0;
		font-weight: bold;
		font-size: 12px;
		text-align: center;
		line-height: $btn2_size;
		white-space: nowrap;
		overflow: hidden;
		cursor: pointer;

		border-radius: 0;
		border: 1px solid transparent;
		cursor: pointer;
		box-shadow: -1px 0 2px 2px rgba(0,0,0,0.02);
		transform: translateX(0);
		&:last-child {
			border-top: 1px solid #F2F5F8;
		}
		span {
			position: relative;
			display: block;
			width: 50px;
			height: 50px;
			&:first-child {
				font-size: 18px;
				float: left;
				border-right: 1px solid #F2F5F8;
			}
			&:nth-child(2) {
				padding-left: 10px;
				float: left;
			}
		}
		&:hover {
			box-shadow: 0 0 4px 3px rgba(0,0,0,0.08);
		}
	}
	.btn-goVNE {
		&:hover {
			transform: translateX(-80px);
			box-shadow: -2px 0 4px 3px rgba(0,0,0,0.08);
			z-index: 2;
		}
	}
	.btn-feedback {
		&:hover {
			transform: translateX(-80px);
			box-shadow: -2px 0 4px 3px rgba(0,0,0,0.08);
			z-index: 2;
		}
	}
}

