.btn.dropdown-toggle::after {
	content: "";
	position: absolute;
	right: 8px;
	margin-top: 8px;
	border-top: 4px dashed;
	border-right: 4px solid transparent;
	border-left: 4px solid transparent;
	// height: 8px;
}
.btn.drilldown {
	position: relative;
	height: 42px;
	padding: 0 24px 0 18px;
	overflow: hidden;
	font-size: 14px;
	text-align: left;
	text-overflow: ellipsis;
	min-width: 220px;
	border: 1px solid #DFE3ED;
	outline: none;
	box-shadow: none;
	b {
		color: #aaaaaa;
	}
}
.hasChild > a:after {
  content: "";
  float: right;
  margin-top: 6px;
  margin-right: -10px;
  border-left: 4px dashed;
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
}
.dropdown-menu>li>a {
	display: block;
	//display: flex;
	padding: 3px 18px;
	clear: both;
	font-weight: normal;
	line-height: 1.4;
	color: #333;
	white-space: nowrap;
	b {
		color: #757575;
		font-family: monospace;
		letter-spacing: -1px;
	}
}