
/* Start chinhs sach - Dieu khoan  css
============================================================================================ */
.section-terms-policy-area {
	position: relative;
	display: block;
	padding: 0;
	@media (min-width: 992px) {
		padding: 30px 0;
	}
	.section-inner {
		padding: 0;
		@media (min-width: 1400px) {
			padding: 0 50px;
			margin-top: 30px;
		}
	}
	.section-box-title {
		position: relative;
		display: flex;
		flex-direction: column;
		width: 100%;
		color: #454545;
		.title-name {
			position: relative;
			display: flex;
			flex-direction: column;
			margin-bottom: 20px;
		}
		.title-sub {
			position: relative;
			display: block;
			width: 100%;
			height: auto;
			font-size: 16px;
			line-height: 1.4;
			margin-bottom: 25px;
		}
	}
	.section-list-items {
		width: 100%;
		.wrap-slide {
			position: relative;
		}
		.list-item-slide {
			position: relative;
			overflow: hidden;
		}
	}

	.wrap-nav-tab {
		@media (max-width: 991px) {
			flex-direction: column;
		}
		.nav {
			flex: none;
			width: 100%;
			padding-right: 0;
			margin-bottom: 30px;
			@media (min-width: 992px) {
				width: 300px;
				padding-right: 10px;
			}
			.nav-link {
				flex: none;
				padding: 8px 0;
				color: #333333;
				outline: none;
				background: none;
				border: none;
				text-align: left;
				&:before {
					content: '';
					display: inline-block;
					flex: none;
					width: 8px;
					height: 8px;
					margin: 12px 15px 0 0;
					background: #C4C4C4;
					border-radius: 8px;
					transition: all 200ms ease-in-out;
				}
				&.active {
					display: flex;
					color: #F27227;
					font-weight: bold;
					&:before {
						width: 33px;
						height: 3px;
						margin: 12px 15px 0 0;
						background-color: #F27227;
					}
				}
			}
		}
		.tab-content {
			position: relative;
			display: block;
			width: 100%;
			.tab-pane {
				@media (max-width: 991px) {
					display: block !important;
					opacity: 1 !important;
				}
				&-inner {
				}
			}
		}
		.tab-pane-inner {
			.title-tab {
				position: relative;
				display: block;
				padding: 5px 40px 5px 15px;
				margin: 30px 0 30px;
				font-weight: 600;
				font-size: 21px;
				line-height: 1.4;
				color: #FFFFFF;
				background: rgba(#F27227,.95);
				border-radius: 5px;
				cursor: pointer;
				&.active {
					display: block;
					&:after {
						content: "\f106";
					}
				}
				&:after {
					content: "\f107";
					font-family: "Font Awesome 5 Pro";
					font-weight: 300;
					-webkit-font-smoothing: antialiased;
					display: inline-block;
					position: absolute;
					height: 20px;
					right: 15px;
					top: 0;
					bottom: 0;
					margin: auto;
					line-height: 20px;
				}
			}
			.wrap-content-menu {
				position: relative;
				display: none;
				list-style: none;
				padding: 0 0 20px;
				margin: 0;
				@media (min-width: 992px) {
					display: block;
				}
				&:before {
					content: '';
					position: absolute;
					display: none;
					z-index: -1;
					bottom: 0;
					width: calc(100% + 30px);
					height: 100%;
					left: -15px;
					background: linear-gradient(-180deg, #FFFFFF 35.21%, #E6EEF4 108.19%);
					@media (max-width: 575px) {
						display: block;
					}
				}
				&.active {
					display: block;
				}
				.label {
					color: #666;
					font-weight: bold;
				}
				.lable {
					color: #666;
				}
				label {
					color: #666;
					font-weight: unset;
					font-size: 14px;
					padding: 0;
					margin-bottom: 0;
				}
				p {
					margin-bottom: 3px;
				}
				img {
					display: block !important;
					max-width: 100%;
					margin: 10px auto;
					height: auto !important;
				}
				table {
					width: 100% !important;
					max-width: 100%;
					tr {
						td {
							padding: 8px 5px;
						}
					}
				}
				iframe {
					max-width: 100% !important;
				}

				.table {
					tr {
						td {
							border: 0;
							padding: 8px 0;
						}
					}
				}
				ul {
					padding-left: 17px;
				}

			}
		}
	}
}
/* End chinhs sach - Dieu khoan  css
============================================================================================ */

