/* Header Area css
============================================================================================ */
.fptOnline {

    .space-header-fixed {
        position: relative;
        display: block;
        width: 100%;
        height: 0;
    }

    header.header { 
        position: relative;
        display: block;
        width: 100%;
        color: $text-header;
        padding: 5px 0 5px 0;
        z-index: 11;
        height: 60px;
        background: #ffffff;
        overflow: hidden;
        &.header-static {
            position: static;
            top: 0;
            background: #ffffff;
        }
        &.header-scroll,
        &.header-fixed {
            position: fixed;
            top: 0;
            background: transparent;
            // box-shadow: 2px 1px 2px rgba(134, 134, 134, 0.5);
        }
        &.add-background {
            background: #ffffff;
            box-shadow: 0 1px 8px rgba(0,0,0,.3);
        }
        &.header-sticky {
            position: sticky;
            top: 0;
            background: #ffffff;
        }
        &.header-not-home {
            position: sticky;
            top: 0;
            background: #1D4F78;
            .header-main .header-right-inner .list-menu-cate .item-menu-cate .menu-link {
                color: #fff;
                font-weight: normal;
                &:hover {
                    color: #FFFFFF;
                }
                &.active {
                    color: #FFFFFF;
                    font-weight: bold;
                }
            }
        }
        a {
            color: inherit;
            outline: none;
        }

        .header-main { 
            position: relative;
            display: block;
            width: 100%;
            clear: both;
            .container {
                display: flex;
                align-items: center;
                justify-content: center;
                .row {
                    width: 100%;
                }
            }
            &-inner {
                display: flex;
                align-items: center;
                @media (min-width: 1400px) {
                    padding: 0 50px;
                }
            }
            .header-left { 
                position: relative;
                display: flex;
                justify-content: space-between;
                align-items: center;
                min-height: 1px;
                user-select: none;
                .box-logo {
                    display: flex;
                    align-items: center;
                    img,svg {
                        max-width: 200px;
                        max-height: 100%;
                        object-fit: scale-down;
                        overflow: hidden;
                    }
                    .logo { 
                        position: relative;
                        display: block;
                        height: 45px;
                        line-height: 45px;
                        z-index: 2;
                        color: #0f0000;
                        text-decoration: none;
                        vertical-align: top;
                        outline: none;
                        h1 {
                            margin: 0;
                            font-weight: 700;
                            font-style: normal;
                            font-size: 24px;
                            line-height: 29px;
                            letter-spacing: -0.05em;
                            text-transform: uppercase;
                            span { 
                                white-space: nowrap;
                                font {
                                    color: #5B8C51;
                                }
                            }
                        }
                    }
                }
            }
            .header-right { 
                position: relative;
                display: block;
                width: 100%;
                min-height: 1px;
                padding-top: 8px;
                user-select: none;
                @media (max-width: 991px) {
                    display: none;
                }
                &-inner {
                    .list-menu-cate{
                        list-style: none;
                        display: flex;
                        flex-wrap: nowrap;
                        justify-content: flex-end;
                        padding: 0;
                        margin: 0 -10px;
                        white-space: nowrap;
                        .item-menu-cate{
                            position: relative;                            
                            display: flex;
                            align-items: center;
                            width: auto;
                            padding: 0 2px;
                            color: #707070;
                            font-size: 16px;
                            line-height: 30px;
                            font-weight: bold;
                            text-align: center;
                            transition: all 300ms linear 0s;
                            vertical-align: middle;
                            // &:not(:last-child) {
                            //     border-right: 1px solid rgba(196, 196, 196, 0.2);;
                            // }
                            @media (min-width: 1200px) {
                                &:nth-last-child(2) {
                                    margin-right: 15%;
                                }
                            }
                            @media (max-width: 1199px) {
                                padding: 0 4px;
                            }
                            &.active {
                                .menu-link {
                                    color: #F27227;
                                    // &:after {
                                    //     background: $bg-btn;
                                    // }
                                }
                            }

                            $height-icon: 20px;
                            .menu-link {
                                position: relative;
                                display: flex;
                                align-items: center;
                                padding: 0 27px;
                                color: inherit;
                                line-height: 30px;
                                border-radius: 4px;
                                overflow: hidden;
                                color: #707070;
                                // &:after {
                                //     $size : 10px;

                                //     content: '';
                                //     position: absolute;
                                //     left: 0;
                                //     right: 0;
                                //     bottom: 14px;
                                //     height: $size;
                                //     width: 100%;
                                //     margin: auto;
                                //     background: transparent;
                                //     z-index: -1;
                                // }
                                .menu-name {
                                    position: relative;
                                }
                                &.active {
                                    color: #F27227;
                                    font-weight: bold;
                                }
                                &:hover {
                                    color: #F27227;
                                    // &:after {
                                    //     background: #411BFF;
                                    // }
                                }
                            }
                            &:hover {

                                &>ul {
                                    display: block;
                                    transform: translateY(0);
                                    opacity: 1;
                                }
                            }

                            &>ul {
                                position: absolute;
                                top: 100%;
                                display: none;
                                padding: 15px 10px;
                                min-width: 126px;
                                background: #FFFFFF;
                                box-shadow: 3px 4px 10px rgba(0, 0, 0, 0.1);
                                border-radius: 10px;
                                text-align: left;
                                font-weight: normal;
                                transform: translateY(50px);
                                opacity: .5;
                                transition: transform .2s all linear;
                                li {
                                    padding: 7px 0;
                                    &:not(:last-child) {
                                        border-bottom: 0.5px solid rgba(0, 0, 0, 0.2);
                                    }
                                    a {
                                        line-height: 1.4;
                                        display: flex;
                                        padding: 4px 10px;
                                        align-items: center;
                                        &:hover {
                                            background: #F8F8F8;
                                        }
                                    }
                                }
                            }

                            &.btn-menu {
                                >span {
                                    height: 36px;
                                    padding: 0 15px;
                                    border-radius: 24px;
                                    cursor: pointer;
                                    &:after {
                                        content: '';
                                        position: relative;
                                        display: block;
                                        width: 18px;
                                        height: 18px;
                                        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='17' height='17' viewBox='0 0 17 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M7.40092 0.201904C3.32009 0.201904 0 3.52395 0 7.60724C0 11.6905 3.32013 15.0126 7.40092 15.0126C11.4817 15.0126 14.8019 11.6905 14.8019 7.60724C14.8019 3.52395 11.4818 0.201904 7.40092 0.201904ZM7.40092 13.7952C3.98901 13.7952 1.21327 11.0191 1.21327 7.6072C1.21327 4.19507 3.98901 1.41918 7.40092 1.41918C10.8128 1.41918 13.5886 4.19507 13.5886 7.6072C13.5886 11.0191 10.8129 13.7952 7.40092 13.7952Z' fill='%23707070'/%3e%3cpath d='M16.604 15.9608L12.6204 11.9842C12.3828 11.7472 11.9988 11.7482 11.7624 11.9864C11.5262 12.2248 11.5272 12.6101 11.7646 12.8473L15.7482 16.8238C15.8667 16.9419 16.0212 17.001 16.1761 17.001C16.332 17.001 16.4877 16.9411 16.6062 16.8216C16.8424 16.5832 16.8414 16.1979 16.604 15.9608Z' fill='%23707070'/%3e%3c/svg%3e");
                                        background-repeat: no-repeat;
                                        background-position: center;
                                    }
                                    &.active:after {
                                        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='17' height='17' viewBox='0 0 17 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M1.70711 1.29289L15.8492 15.435M1.29289 15.2929L15.435 1.15076' stroke='%23aaaaaa' stroke-width='2'/%3e%3c/svg%3e");
                                    }
                                }
                            }

                        }
                    }
                }
            }
        }
        @media (min-width: 992px) {
            .header-main .header-left {
                .box-btn-show-menu {
                    display: none;
                }
                .box-btn-show-search {
                    display: none;
                }
            }
        }

        @media (max-width: 991px) {
            height: auto;
            padding-bottom: 5px;
            padding: 0;
            .header-main { 
                .header-left {
                    width: 100%;
                    float: left;
                    .box-logo {
                        text-align: center;
                        margin: 0 auto;
                        .logo {
                            position: relative;
                            display: flex;
                            align-items: center;
                            height: 50px;
                            padding: 5px 0;
                            line-height: 50px;
                            h1 {
                                font-size: 21px;
                                line-height: 29px;
                                letter-spacing: -0.05em;
                            }
                        }
                        svg {
                            width: 117px;
                            height: 50px;
                        }
                    }

                    .box-btn-show-menu {
                        position: absolute;
                        top: 0;
                        left: -24px;
                        z-index: 2;
                        display: block;
                        width: 55px;
                        height: 50px;
                        padding: 13px 15px;
                        background: none;
                        border: none;
                        box-shadow: none;
                        outline: none;
                        cursor: pointer;
                        span {
                            display: block;
                            background: var(--primary-1);
                            height: 2px;
                            width: 25px;
                            margin-top: 4px;
                            margin-bottom: 4px;
                            transform: rotate(0deg);
                            position: relative;
                            left: 0;
                            opacity: 1;
                            pointer-events: none;
                            &:nth-child(1), 
                            &:nth-child(3) {
                                transition: transform .35s ease-in-out;
                                transition: transform .35s ease-in-out, -webkit-transform .35s ease-in-out, -o-transform .35s ease-in-out;
                            }
                        }
                        &.active {
                            span {
                                &:nth-child(1) {
                                    position: absolute;
                                    top: 0;
                                    bottom: 0;
                                    left: 0;
                                    right: 0;
                                    margin: auto;
                                    transform: rotate(135deg);
                                    opacity: 0.9;
                                }
                                &:nth-child(2) {
                                    height: 3px;
                                    visibility: hidden;
                                    background-color: transparent;
                                }
                                &:nth-child(3) {
                                    position: absolute;
                                    top: 0;
                                    bottom: 0;
                                    left: 0;
                                    right: 0;
                                    margin: auto;
                                    transform: rotate(-135deg);
                                    opacity: 0.9;
                                }
                            }
                        }
                    }
                    .box-btn-show-search {
                        position: absolute;
                        top: 0;
                        right: -24px;
                        z-index: 2;
                        display: block;
                        width: 55px;
                        height: 50px;
                        padding: 13px 15px;
                        background: transparent;
                        border: none;
                        box-shadow: none;
                        outline: none;
                        cursor: pointer;
                        .btn-show-search {
                            display: inline-block;
                            width: 24px;
                            height: 24px;
                            color: var(--primary-1);
                            pointer-events: none;
                            background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='21' height='21' viewBox='0 0 21 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M14.7594 8.12971C14.7594 11.7912 11.7912 14.7594 8.12971 14.7594C4.46822 14.7594 1.5 11.7912 1.5 8.12971C1.5 4.46822 4.46822 1.5 8.12971 1.5C11.7912 1.5 14.7594 4.46822 14.7594 8.12971ZM12.7195 14.8408C11.4133 15.7358 9.83266 16.2594 8.12971 16.2594C3.63979 16.2594 0 12.6196 0 8.12971C0 3.63979 3.63979 0 8.12971 0C12.6196 0 16.2594 3.63979 16.2594 8.12971C16.2594 9.83266 15.7358 11.4133 14.8408 12.7195L20.03 17.9086L17.9086 20.03L12.7195 14.8408Z' fill='%23707070'/%3e%3c/svg%3e");
                            background-position: center;
                            background-repeat: no-repeat;
                        }
                        &.active {
                            .btn-show-search {
                                background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='17' height='17' viewBox='0 0 17 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M1.70711 1.29289L15.8492 15.435M1.29289 15.2929L15.435 1.15076' stroke='%23888888' stroke-width='2'/%3e%3c/svg%3e");
                            }
                        }
                    }
                }
                .header-right {
                    display: none;
                }
            }
        }
    }




    .box-breadcrumb {
        position: relative;
        display: block;
        display: flex;
        align-items: center;
        flex-wrap: nowrap;
        width: 100%;
        height: 35px;
        background: #F7F0EA;
        font-size: 16px;
        padding: 0 30px;
        white-space: nowrap;
        overflow-x: auto;
        .item-breadcrumb {
            position: relative;
            margin-right: 30px;
            color: inherit;
            &+.item-breadcrumb {
                &:before {
                    content: "|";
                    color: $txt_trang;
                    position: absolute;
                    top: 0;
                    left: -15px;
                }
            }
            &:last-child {
                font-weight: normal;
            }
            a {
                color: inherit;
                text-decoration: none;
            }
        }
    }
    

}
/* End Header Area css
============================================================================================ */


/* Menu Mobile Area css
============================================================================================ */
    .aside_area {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        right: auto;
        bottom: auto;
        z-index: 9;
        display: none;
        &.show {
            display: block;
        }
        .bg_drop {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            background-color: rgba(0,0,0,.3)
        }
        .nav-aside {
            position: relative;
            width: 100%;
            height: 100%;
            max-width: 460px;
            padding-top: 50px;
            // background: rgba(#1169b0,.3);
            // background-color: #eaecee;
            background-color: #f2f5f8;
            z-index: 2;
            overflow-y: auto;
            >ul.list-menu-cate {
                list-style: none;
                width: 100%;
                display: block;
                padding: 10px 18px 0 18px;
                margin: 0;
                background-color: #F2F5F8;
                overflow-y: auto;
                >li {
                    position: relative;
                    width: 100%;
                    clear: both;
                    &:not(:last-child) {
                        border-bottom: 1px solid rgba(196, 196, 196, 0.2);
                    }
                    a {
                        position: relative;
                        display: block;
                        display: flex;
                        align-items: center;
                        padding: 13px 0;
                        font-size: 16px;
                        color: #3C3241;
                        line-height: 1.4;
                        width: calc(100% - 50px);
                        &:focus {
                            color: $color-highlight;
                        }
                        .menu-icon {
                            width: 35px;
                        }
                        svg {
                            path {
                                fill: $color-highlight;
                            }
                        }
                    }
                    .btn-toggle {
                        position: absolute;
                        top: 0;
                        right: 0;
                        z-index: 2;
                        background: #96cbd4;
                        border-left: 1px solid #eaeaea;
                        width: 50px;
                        height: 48px;
                        cursor: pointer;
                        &:before {
                            content: "";
                            position: absolute;
                            display: block;
                            top: 23px;
                            left: 15px;
                            width: 20px;
                            height: 2px;
                            background: #fff;
                        }
                        &:after {
                            content: "";
                            position: absolute;
                            display: block;
                            top: 14px;
                            left: 24px;
                            width: 2px;
                            height: 20px;
                            background: #fff;
                        }
                        &.active {
                            &:after {
                                content: "";
                                visibility: hidden;
                            }
                        }
                    }

                    .menu-list-lv2 {
                        display: none;
                        list-style: none;
                        width: 100%;
                        padding: 0 0 0 20px;
                        border-left: 1px dashed #dddddd;
                        .btn-toggle {
                            //display: none;
                            &.active {
                                display: block;
                            }
                        }
                        &.active {
                            display: block;
                        }
                        >li {
                            position: relative;
                            border-top: 1px solid rgba(196, 196, 196, 0.2);
                        }
                    }
                }
            }

            .list-socail-ft {
                display: flex;
                align-items: center;
                list-style: none;
                padding: 0;
                margin: 0 auto;
                padding: 10px 10px;
                font-size: 21px;
                text-align: center;
                border-top: 1px solid #e8ebed;
                li {
                    margin-right: 15px;
                    &:last-child {
                        margin-right: 0;
                    }
                    @media (min-width: 768px) {
                        margin-bottom: 8px;
                    }
                    a {
                        display: block;
                        width: 40px;
                        height: 40px;
                        padding: 0;
                        font-size: 24px;
                        line-height: 40px;
                        white-space: nowrap;
                        color: #1169b0;
                        opacity: 50%;
                        &:hover {
                            opacity: 1;
                            //background-image: linear-gradient(to top, #9a9a9a, #b2b2b2, #cbcbcb, #e5e5e5, #ffffff);
                            i {
                                &.fa-facebook-square {
                                    color: #2d88ff;
                                }
                                &.fa-twitter-square {
                                    color: #1da1f2;
                                }
                                &.fa-instagram {
                                    color: #d82b7e;
                                }
                                &.fa-youtube-square {
                                    color: #ff0000;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
/* End Menu Mobile Area css
============================================================================================ */


/* Form Search Area css
============================================================================================ */
    .form-search {
        position: relative;
        display: block;
        width: 100%;
        max-width: 445px;
        min-height: 36px;
        margin: 0 auto;
        border: 0;
        border-radius: 0;
        overflow: visible;
        background: transparent;
        border: 1px solid transparent;
        .input-search {
            width: calc(100% - 100px);
            max-width: 100%;
            height: 34px;
            float: left;
            border: 0;
            padding: 0 5px 0 18px;
            outline: none;
            font-size: 14px;
            color: $txt_default;
            background: #fff;
            border: 1px solid $border-title-highlight;
            border-right: 0;
            border-radius: 0;
            -webkit-transition: border-color ease-in-out .15s,-webkit-box-shadow ease-in-out .15s;
            -o-transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
            transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
            &::placeholder {
                color: #dcd6d6;
                font-style: italic;
                font-size: 12px;
                user-select: none;
            }
            &:hover,&:focus {
                border-color: $border_trang !important;
                border-right: 0;
            }
            &.active {
                &~.wrap-show-result {
                    display: block;
                }
            }
        }
        .btn-search {
            position: relative;
            width: 100px;
            height: 34px;
            line-height: 34px;
            float: left;
            padding: 1px 6px;
            border: 0;
            background: $bg-btn;
            color: #fff;
            outline: none;
            font-size: 12px;
            border-radius: 0;
            span {
                margin-right: 3px;
            }
        }
        .easy-autocomplete-container,
        .wrap-show-result {
            position: absolute;
            display: block;
            width: 100%;
            background: $bg_trang;
            color: #333;
            padding: 0 3px 0 10px;
            box-shadow: 1px 1px 5px 0 #ccc;
            border-radius: 12px;
            z-index: 999;
            top: 36px;
            overflow: hidden;
            &:before,
            &:after {
                content: "";
                display: block;
                position: absolute;
                z-index: 2;
                left: 0;
                width: 100%;
                height: 8px;
            }
            &:before {
                top: 0;
                background: linear-gradient(180deg, rgba(0, 0, 0, 0.05), transparent);
            }
            // &:after {
            //     bottom: 0;
            //     background: linear-gradient(0deg, rgba(0, 0, 0, 0.05), transparent);
            // }
            ul {
                position: relative;
                display: none;
                top: 0;
                width: 100%;
                // max-height: 510px;
                margin: 0;
                padding: 10px 7px 0 0;
                border: 0;
                overflow: hidden;
                overflow-y: auto;
                &::-webkit-scrollbar {
                    width: 5px;
                    height: 3px;
                    background-color: #eaeaea;
                    border-radius: 2.5px;
                    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.2);
                }
                &::-webkit-scrollbar-track {
                    width: 5px;
                    height: 3px;
                    background-color: #eaeaea;
                    border-radius: 2.5px;
                    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.2);
                }
                &::-webkit-scrollbar-thumb {
                    background-color: $bg-color;
                    border-radius: 2.5px;
                }
            }
            li,
            .item {
                position: relative;
                list-style: none;
                margin-bottom: 2px;
                width: 100%;
                display: block;
                float: left;
                padding: 3px 5px;
                border: 0;
                &:hover,.active {
                background: rgba(0, 0, 0, 0.04)
                }
                a {
                    font-size: 14px;
                    color: #333;
                    line-height: 20px;
                    display: block;
                    cursor: pointer;
                }
                img {
                    float: left;
                    width: 60px;
                    padding: 2px;
                    margin-right: 0;
                }
                .warp {
                    float: left;
                    width: calc(100% - 60px);
                    padding-left: 10px;
                    .title {
                        display: -webkit-box;
                        font-size: 15px;
                        max-height: 36px;
                        word-break: break-word;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 2;
                    }
                    .price {
                        color: $color-highlight;
                        margin-top: 2px;
                    }
                }
                .title {
                    line-height: 18px;
                }
                .clear {
                    font-size: 12px;
                    position: absolute;
                    top: 5px;
                    right: 8px;
                    color: #F44336;
                    text-decoration: none;
                    cursor: pointer;
                    &:hover {
                        font-weight: bold;
                    }
                }
            }
        }
    }
/* End Form Search Area css
============================================================================================ */




/* Search Area css
============================================================================================ */
.search_area {
    position: relative;
    display: none;
    width: 100%;
    background-color: $bg-color;
    z-index: 3;
    &.show {
        display: block;
    }
    .search-area-inner {
        position: relative;
        display: block;
        width: 100%;
        padding: 10px 0;
        .form-search {
            position: relative;
            display: block;
            // background-color: #FFFFFF;
            // border: 1px solid #DFE3ED;
            // border-radius: 18px;
            .form-group {
                position: relative;
                display: block;
                width: 100%;
                height: 36px;
                background-color: #FFFFFF;
                border: 1px solid #DFE3ED;
                border-radius: 18px;
                margin-bottom: 0;
            }
            .input-search {
                float: left;
                width: calc(100% - 44px);
                height: 34px;
                line-height: 34px;
                padding: 0 15px;
                border: 0;
                border-radius: 20px 0 0 20px;
                outline: none;
                box-shadow: none;
                &::placeholder {
                    font-size: 12px;
                    color: $text-primary-10;
                    font-style: italic;
                    letter-spacing: -0.24px;
                }
            }
            .btn-search {
                position: relative;
                float: left;
                width: 44px;
                height: 34px;
                border: 0;
                border-radius: 0 20px 20px 0;
                outline: none;
                box-shadow: none;
                background-color: $bg_trang;
                background-image: $url_bg_btn_search;
                background-repeat: no-repeat;
                background-position: center;
                &:before {
                    content: "";
                    position: absolute;
                    display: block;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    margin: auto;
                    width: 1px;
                    height: 10px;
                    background-color: #DFE3ED;
                }
            }
        }
    }
    @media (min-width: 992px) {
        display: none;
        padding: 10px;
    }
}
/* End Search Area css
============================================================================================ */

