/*===================================*
  16.START BREADCRUMB STYLE
*===================================*/
	.wrapper-breadcrumb-area {
		position: relative;
		display: flex;
		align-items: flex-end;
		width: 100%;
		min-height: 154px;
		padding-bottom: 10px;
		overflow: hidden;
		color: #fff;
		text-shadow: -1px 1px 1px rgba(0, 0, 0, 0.3);
		background-position: center !important;
		@media (min-width: 768px) {
			min-height: 240px;
			padding-bottom: 30px;
			background-position: left !important;
		}
		@media (min-width: 1200px) {
			min-height: 383px;
			padding-bottom: 80px;
		}
		&.bg_default_01 {
			background-image: url('../images/banner_land.jpg');
			background-repeat: no-repeat;
			background-position: center;
		}
		&.bg_default_02 {
			background-image: url('../images/banner_land_02.jpg');
			background-repeat: no-repeat;
			background-position: center;
		}
		&.bg_default_03 {
			background-image: url('../images/banner_land_03.jpg');
			background-repeat: no-repeat;
			background-position: center;
		}
		&.bg_default_04 {
			background-image: url('../images/banner_land_04.jpg');
			background-repeat: no-repeat;
			background-position: center;
		}
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background-image: linear-gradient(0deg , #027385, rgba(2, 115, 133,.1));
			opacity: 0.6;
		}
		&-inner {
			position: relative;
			display: block;
			@media (min-width: 1400px) {
				padding: 0 50px;
			}
		}
		.breadcrumb-content {
			position: relative;
			width: 100%;
			display: flex;
			flex-direction: column;
			z-index: 1;
			.title-area {
				margin-bottom: 10px;
				font-size: 25px;
				line-height: 1.4;
				@media (min-width: 768px) {
					font-size: 32px;
				}
				@media (min-width: 1200px) {
					font-size: 50px;
				}
			}
			.des-area {
				display: flex;
				margin-bottom: 10px;
				font-size: 17px;
				font-weight: 500;
				line-height: 1.4;
				@media (min-width: 768px) {
					font-size: 19px;
				}
				&:before {
					content: '';
					flex: none;
					width: 15.7px;
					height: 1.33px;
					margin: 12px 7px 0 0;
					background-color: #FFFFFF;
					@media (min-width: 768px) {
						width: 46.2px;
						height: 1.6px;
						margin: 12px 15px 0 0;
					}
				}
			}
		}
	}

	.box-breadcrumb-pc { 
		padding: 10px 0;
		width: 100%;
		.breadcrumb { 
			display: flex;
			background-color: transparent;
			margin: 0;
			padding: 0;
			.breadcrumb-item { 
				font-size: 14px;
				line-height: 20px;
				& + .breadcrumb-item::before {
					content: "\f105";
					font-family: "Font Awesome 5 Pro";
					vertical-align: middle;
					color: #fff;
				}
				&.active {
					color: #fff;
				}
				a { 
					display: inline-block;
					color: #fff;
				}
			}
		}
	}
	
	// BREADCRUMB Bottom Area
	.section-breadcrumb {
		font-size: 14px;
		margin-top: 15px;
	}
/*===================================*
  16.END BREADCRUMB STYLE
*===================================*/


/*===================================*
  16.START BREADCRUMB STYLE
*===================================*/
.breadcrumb-area {
	// position: absolute;
	// left: 0;
	// top: initial;
	// z-index: 2;
	display: block;
	width: 100%;
	// padding: 5px 0;
	margin: 0;
	color: $text-primary-10;
	background: transparent;
	// @media (min-width: 1400px) {
	// 	min-height: 41px;
	// }
	.container {
		position: relative;
	}
	.breadcrumb-inner {
		position: relative;
		list-style: none;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		width: 100%;
		margin: 0 auto;
		padding: 0;
		background: none;
		font-size: 15px;
		border-radius: 0;
		border: 0;
		@media (min-width: 1400px) {
			padding: 0 50px;
		}
		@media (min-width: 1200px) {
			position: absolute;
			top: initial;
			z-index: 2;
		}
		>li {
			position: relative;
			display: inline-block;
			float: none;
			display: flex;
			align-items: center;
			padding: 0 15px;
			line-height: 21px;
			&:first-child {
				padding-left: 0;
			}
			&+li {
				&:before {
					content: "";
					position: absolute;
					z-index: 2;
					top: 0;
					bottom: 0;
					margin: auto;
					left: 0;
					width: 1px;
					height: 13px;
					padding: 0;
					text-align: center;
					background: #707070;
					transform: rotate(15deg);
				}
			}
			&.active {
				color: #024173;
				white-space: nowrap;
				text-overflow: ellipsis;
				overflow: hidden;
				display: block;
				// overflow: hidden;
				a {
					color: #024173;
				}
			}
			&.dropdown {
				padding-right: 25px;
			}
			.icon-back-url {
				display: -webkit-inline-box;
				display: inline-block;
				position: absolute;
				height: 40px;
				width: 25px;
				top: -7px;
				left: -10px;
				line-height: 40px;
				text-align: center;
			}
			a {
				padding: 5px 0;
				color: #707070;
				&:hover {
					color: $color-highlight;
				}
			}
			&:hover {
				ul.dropdown-menu {
					display: block;
				}
			}
		}
	}
}
/*===================================*
  16.END BREADCRUMB STYLE
*===================================*/


/* Home Pagination Area css
============================================================================================ */
.pagination-area {
	position: relative;
	display: block;
	text-align: center;
	font-size: 14px;
	padding: 15px 0;
	margin-bottom: 10px;
	color: #000;
	.form-inner {
		margin: 0 auto;
		display: flex;
		justify-content: center;
		align-items: center;
		button,
		input {
			border: 1px solid #411BFF;
			border-radius: 5px;
			outline: none;
			box-shadow: none;
		}
		.search-page {
			min-width: 30px;
			max-width: 46px;
			height: 30px;
			margin: 0 7px;
			line-height: 30px;
			background: #F2F5F8;
			color: #212121;
			border-radius: 5px;
			text-align: center;
			//font-weight: 700;
			&:hover,
			&:focus {
				background: rgba(65, 27, 255, .2);
			}
		}
		.btn-prev-pagination,
		.btn-next-pagination {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 30px;
			height: 30px;
			padding: 0 6px;
			line-height: 30px;
			background-color: $bg-color;
			font-size: 16px;
			color: #FFFFFF;
			border: 1px solid #411BFF;
			border-radius: 5px;
			outline: none;
			text-align: center;
			font-weight: 700;
			&:after {
				content: "\f0d9";
				font-family: 'Font Awesome 5 Pro';
				font-weight: 700;
				-moz-osx-font-smoothing: grayscale;
				-webkit-font-smoothing: antialiased;
				display: inline-block;
				font-style: normal;
				font-variant: normal;
				text-rendering: auto;
				line-height: inherit;
			}
			&:hover {
				background-color: $bg-color;
				color: $txt_trang;
			}
			&--disable {
				background-color: #F2F5F8;
				color: #9B9B9B;
				border-color: #b8b8b8;
				pointer-events: none;
				opacity: 0.8;
				&:hover {
					background-color: inherit;
					color: $txt_trang;
				}
			}
		}
		.btn-prev-pagination {
			margin: 0 7px;
		}
		.btn-next-pagination {
			transform: rotateZ(180deg);
			margin: 0 7px;
		}
	}
}

	// Start .pagination option 2
	.box-pagination {
		text-align: center;
	}
	ul.pagination {
		display: block;
		display: inline-block;
		padding-left: 0;
		padding: 15px 20px 5px;
		margin: 0;
		border-radius: 4px;
		list-style: none;
		float: none !important;
		li {
			display: inline;
			a {
				position: relative;
				min-width: 34px;
				width: auto !important;
				height: auto !important;
				line-height: 30px;
				float: left;
				padding: 0 7px !important;
				margin-left: -1px !important;
				color: #9B9B9B;
				text-decoration: none !important;
				border: 1px solid transparent !important;
				background: #EBEDF4;
				border-radius: 0;
				font-size: 14px;
				&:hover {
					background: rgba(#F27227, .3);
					color: #555;
				}
				&.active {
					background: #F27227;
					color: #fff !important;
				}
				i {
					font-weight: 300;
				}
			}
			&:not(:last-child) {
				a {
					margin-right: 5px;
				}					
			}
		}
	}
	// End .pagination option 2
