.ldp-hoz-scrollable-menu {
	position: relative;
	display: block;
	width: 100%;
	overflow: hidden;
	z-index: 8;
	top: 40px;
	@media (max-width: 991px) {
		display: none !important;
	}
    .ldp-pn-ProductNav_Wrapper {
		position: relative;
		height: 50px;
		@media (min-width: 1400px) {
			padding: 0 50px;
		}
	}

	.ldp-pn-ProductNav {
		position: relative;
		display: block;
		width: 100%;
		margin-bottom: 0;
		height: 50px;
		float: left;
		background-color: rgba(242, 114, 39, .9);
		overflow-x: auto;
		overflow-y: hidden;
		-webkit-overflow-scrolling: touch;
		white-space: nowrap;
		-ms-overflow-style: -ms-autohiding-scrollbar;
		&::-webkit-scrollbar {
			display: none;
		}
		@media (max-width: 767px) {
			height: 60px;
		}
	}

	.ldp-pn-ProductNav_Contents {
		position: relative;
		display: block;
		transition: transform 0.2s ease-in-out;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 5px 10px;
		margin: 0 auto;
		text-align: center;
		cursor: pointer;
		max-width: 100%;
		overflow-x: auto;
		@media (max-width: 767px) {
			margin: 3px auto 0;
		}
		&.active {
			background: rgba(255,255,255,0.1);
			cursor: grabbing;
			cursor: -webkit-grabbing;
		}
		&::-webkit-scrollbar {
			display: none;
		}
	}

	.ldp-pn-ProductNav_Contents-no-transition {
		transition: none;
	}

	.ldp-pn-ProductNav_Link {
		display: flex;
		align-items: center;
		justify-content: center;
		min-width: 100px;
		height: 40px;
		padding: 0 5px;
		margin-right: 5px;
		font-size: 12.5px;
		text-align: center;
		text-decoration: none;
		white-space: nowrap;
		color: #fff;
		border: 1px solid transparent;
		border-radius: 34px;
		font-family: -apple-system, sans-serif;
		& + & {
			border-left-color: #eee;
		}
		&.active {
			font-weight: bold;
			color: #fee512;
			background: rgba(255, 255, 255, 0.2);
			svg path[fill] {
				fill: #fee512;
			}
		}
		&:not(.active):hover {
			color: #ffffff;
			background: rgba(255, 255, 255, 0.4);
		}
	}

	.ldp-pn-ProductNav_Indicator {
		position: absolute;
		bottom: 0;
		left: 0;
		height: 4px;
		width: 100px;
		background-color: transparent;
		transform-origin: 0 0;
		transition: transform 100ms ease-in-out, background-color 100ms ease-in-out;
	}

}
.ldp-menu-fixed {
	position: fixed !important;
	z-index: 9;
	width: 100%;
	height: 50px;
	margin: auto;
	background-color: rgba(242, 114, 39, .9);
	box-shadow: 0 1px 3px #00000038;
	top: 50px;
	@media (min-width: 992px) {
		top: 60px;
	}
	@media (max-width: 767px) {
		display: none !important;
	}
}
.space-ldp-nav-top-fixed {
	position: relative;
	display: none;
	width: 100%;
	clear: both;
	height: 50px;
	@media (max-width: 767px) {
		display: none !important;
	}
	&.show {
		display: block;
	}
}