.section-eclick-area-inner .row-item .col-left .circle-box-animate{
	overflow: inherit !important;
	top: 20px;
}

.section-eclick-area.bg-solution-eclick{
	overflow: hidden;
}

.rotate--circle{
	position: relative;
}

.rotate--circle .prev{
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
}

.rotate--circle .next{
	position: absolute;
	top: 0;
	right: 0;
	opacity: 0;
}

.circle--rotate{
	border-radius: 50%;
	margin: 0 auto 0;
	position: relative;
	transform: rotate(0deg);
	z-index: 0;
	padding: 0;
	width: 100%;
	max-width: 600px;
}

.circle--rotate > li{
	position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 50%;
    z-index: 10;
	text-align: center;
	cursor: pointer;
	visibility: hidden;
}

.circle--rotate .icon{
	position: relative;
	width: 120px;
	height: 120px;
	margin: -60px auto;
	visibility: visible;
}

.circle--rotate .icon .img{
	background-repeat: no-repeat;
	background-position: center 0;
	width: 100%;
	height: 100%;
	transform: scale(0.5);
	-webkit-transform: scale(0.5);
	-ms-transform: scale(0.5);
	img {
		max-width: 100%;
		max-height: 100%;
		object-fit: scale-down;
	}
}

.circle--rotate .block.active .img{
	transform: scale(0.8);
	-webkit-transform: scale(0.8);
	-ms-transform: scale(0.8);
}
.circle--rotate .block.active{
	z-index: 20;
}

.circle--rotate .icon:hover .img,.circle--rotate .block.active .img{
	background-position: center -120px;
}

