.circle--rotate .t-content{
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	opacity: 0;
	margin-top: -23px;
	transition: all 0.5s linear;
	-webkit-transition: all 0.5s linear;
	-moz-transition: all 0.5s linear;
}

.circle--rotate .block.active .t-content{
	opacity: 1;
}

.circle--rotate .t-content h4{
	font-size: 14px;
	font-weight: 900;
	margin-bottom: 0;
}

.circle--rotate .block.active:hover .t-content h4{
	color: #1169B0;
}

.circle--rotate .t-content p{
	font-size: 12px;
	margin-top: 2px;
	margin-bottom: 0;
	line-height: 16px;
}

.circle--rotate .t-content a{
	font-size: 11px;
	margin-bottom: 0;
}

.circle--rotate .block a{
	display: none;
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	z-index: 2;
}

.circle--rotate .block.active a{
	display: block;
}

