.rotate--circle .eclick-image{
	position: absolute;
	left: 50%;
	top: 55px;
	width: 100%;
	transform: translate(-50%,0);
	-webkit-transform: translate(-50%,0);
	-moz-transform: translate(-50%,0);
	display: flex;
	justify-content: center;
	z-index: -1
}

.rotate--circle .eclick-image img{
	height: inherit !important;
	max-width: 100%;
	min-height: auto;
}

@media (max-width: 1199px){
	.section-eclick-area-inner .row-item .col-left .circle-box-animate{
		margin: 0 auto 70px;
		top: 0;
	}

	.section-eclick-area-inner .row-item .col-left{
		position: relative;
	}

	.section-eclick-area-inner .row-item .col-left::after{
		content: '';
		position: absolute;
		bottom: 0px;
	    left: 0px;
	    width: 100%;
	    // height: 245px;
    	background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255,1),rgba(255, 255, 255,1));
	}

	.section-eclick-area-inner .row-item .col-right{
		margin-top: -30px;
	}
}

@media (max-width: 767px){
	.section-eclick-area-inner .row-item .col-left .circle-box-animate{
		height: auto !important;
	}

	.rotate--circle .eclick-image img{
		max-width: calc(100% - 115px) !important;
	}
}

@media (max-width: 600px){
	.circle--rotate{
		max-width: calc(100% - 80px);
	}

	.circle--rotate .icon .img{
		transform: scale(0.4);
	    -webkit-transform: scale(0.4);
	    -ms-transform: scale(0.4);
	}

	.circle--rotate .block.active .img{
		transform: scale(0.6);
		-webkit-transform: scale(0.6);
		-ms-transform: scale(0.6);
	}

	.rotate--circle .eclick-image img{
		max-width: calc(100% - 150px) !important;
	}

	.rotate--circle .eclick-image{
		top: 50%;
		transform: translate(-50%,-50%);
		-webkit-transform: translate(-50%,-50%);
		-moz-transform: translate(-50%,-50%);
	}

	.circle--rotate .t-content p{
		display: none;
	}

	.circle--rotate .t-content{
		margin-top: -35px;
	}

	.circle--rotate .t-content h4{
		font-size: 12px;
	}

	.section-eclick-area-inner .row-item .col-left::after{
		bottom: 40px;
	}

	.section-eclick-area-inner .row-item .col-right{
		margin-top: -40px;
	}
}

@media (max-width: 500px){
	.section-eclick-area-inner .row-item .col-left::after{
		bottom: -30px;
	}

	.section-eclick-area-inner .row-item .col-right{
		z-index: 5;
		margin-top: -70px;
	}

	.section-eclick-area-inner .row-item .col-left::after{

	    margin-left: -15px;
    	width: calc(100% + 30px);
	}

	.circle--rotate{
		height: 483px !important;
	}

	.rotate--circle .eclick-image img{
		max-width: 100% !important;
	}

	.circle--rotate .t-content p{
		display: block;
	}
}

@media (max-width: 450px){
	.circle--rotate .icon .img{
		transform: scale(0.3);
	    -webkit-transform: scale(0.3);
	    -ms-transform: scale(0.3);
	}

	.circle--rotate .block.active .img{
		transform: scale(0.5);
		-webkit-transform: scale(0.5);
		-ms-transform: scale(0.5);
	}

	.section-eclick-area-inner .row-item .col-left .circle-box-animate{
		margin: 0 auto;
	}

	.circle--rotate .icon{
		width: 100px;
		height: 100px;
	}

	.circle--rotate .t-content{
		margin-top: -22px;
	}
}