.fptOnline {

	// Start Widget Sidebar Area
	.area-sidebar-right {

		// This class Publish Widget
		.sidebar-widget {
			position: relative;
			width: 100%;
		}
		// End class Publish Eidget


		// Start Widget IMG ADS
		.img-ads {
			text-align: center;
			position: sticky;
			top: 0px;
			margin-bottom: 20px;
			&.box-sticky {
				position: sticky;
				top: 0px;
				margin-bottom: 20px;
			}
			img {
				width: auto;
				margin: 0 auto;
				max-width: 100%;
			}
		}
		// End Widget IMG ADS


		// Start Widget box Bao cao tai chinh Area _ page QuanHeCongDong
		.box-bctc-area {
			position: relative;
			display: block;
			width: 100%;
			background: rgba(196, 196, 196, 0.1);
			.sidebar-title {
				position: relative;
				display: block;
				padding: 15px 15px;
				color: #333333;
				background: #D6D6D6;
				border-radius: 5px 5px 0 0;
				@media (min-width: 1200px) {
					padding: 15px 25px;
				}
				.title-name {
					margin: 0;
					font-size: 20px;
					line-height: 1.3;
					font-weight: bold;
					color: inherit;
				}
			}
			.list-item-post {
				position: relative;
				display: flex;
				flex-wrap: wrap;
				flex-direction: column;
				width: 100%;
				padding: 25px 15px 0;
				@media (min-width: 1200px) {
					padding: 25px 25px 0;
				}
				.item-post { 
					position: relative;
					display: flex;
					flex-direction: column;
					width: 100%;
					margin: 0 0 25px;
					padding: 0 0 20px;
					font-size: 16px;
					border-bottom: 1px dashed #B4B4B4;
					&:after {
						border: 1px dashed #B4B4B4;
					}

					.item-info { 
						position: relative;
						width: 100%;
						.info-title { 
							position: relative;
							width: 100%;
							margin: 0 0 15px;
							font-size: 16px;
							line-height: 1.3;
							font-weight: 600;
							color: #333333;
						}
						.publish-time {
							padding: 5px 0;
							color: #707070;
							font-size: 14px;
						}
						.btn-download-file {
							display: inline-flex;
							align-items: center;
							justify-content: center;
							font-size: 14px;
							line-height: 1.3;
							font-weight: 600;
							color: #F27227;
							text-decoration: none;
							cursor: pointer;
							&:after {
								content: "\f175";
								position: relative;
								padding-left: 10px;
								text-align: center;
								vertical-align: middle;
								z-index: 2;
								font-family: 'Font Awesome 5 Pro';
								font-size: 16px;
								font-weight: 500;
								-moz-osx-font-smoothing: grayscale;
								-webkit-font-smoothing: antialiased;
								font-style: normal;
								font-variant: normal;
								text-rendering: auto;
							}
						}
					}
				}
			}
		}
		// End Widget box Bao cao tai chinh Area _ page QuanHeCongDong



	}
	// End Widget Sidebar Area

	// Start Widget Sidebar Area
	.area-sidebar-left {
		.nav-menu-sidebar {
			ul {
				list-style: none;
				margin: 0;
				padding: 0;
				li {
					.nav-link {
						flex: none;
						padding: 8px 0;
						color: #333333;
						outline: none;
						background: none;
						border: none;
						text-align: left;
						display: flex;
						&:before {
							content: '';
							display: inline-block;
							flex: none;
							width: 8px;
							height: 8px;
							margin: 10px 15px 0 0;
							background: #C4C4C4;
							border-radius: 8px;
							transition: all 200ms ease-in-out;
						}
						&.active {
							display: flex;
							color: #F27227;
							&:before {
								width: 33px;
								height: 3px;
								margin: 12px 15px 0 0;
								background-color: #F27227;
							}
						}
					}
				}
			}
		}

	}
	// End Widget Sidebar Area
	// 

	.area-sidebar-left ,
	.area-sidebar-right {
		.list-item-post.list-item-post-related-vertical {
			position: relative;
			display: flex;
			flex-wrap: wrap;
			flex-direction: column;
			width: 100%;
			.item-post { 
				position: relative;
				display: flex;
				flex-direction: column;
				width: 100%;
				margin: 0 0 20px;
				padding: 0 0 15px;
				font-size: 16px;
				border-bottom: 1px dashed #B4B4B4;
				&:last-child {
					border-bottom: 0;
					margin-bottom: 0;
				}
				&:after {
					border: 1px dashed #B4B4B4;
				}
				>.item-image-a,
				>.item-image {
					flex: none;
					position: relative;
					display: block;
					width: 100%;
					margin-bottom: 15px;
					border-radius: 4px;
					overflow: hidden;

					img { 
						width: 100%;
						height: auto;
						max-height: 280px;
						object-fit: cover;
						@media (min-width: 992px) {
							max-height: 240px;
						}
					}
				}

				.item-info { 
					position: relative;
					width: 100%;
					.publish-time {
						padding: 5px 0;
						color: #707070;
						font-size: 14px;
						font-weight: 500;
					}
					.info-title { 
						position: relative;
						width: 100%;
						margin: 0;
						font-size: 19px;
						line-height: 1.3;
						font-weight: 600;
						@media (min-width: 1400px) {
							font-size: 22px;
						}
						a { 
							position: relative;
							display: -webkit-box;
							width: 100%;
							color: #333333;
							&:hover {
								color: #2aa9e0;
							}
						}
					}
				}
			}
		}
		.btn-viewmore {
			position: relative;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			min-width: 150px;
			height: 40px;
			padding: 0 6px 0 12px;
			margin: 10px auto 30px;
			color: #F27227;
			background-color: transparent;
			border: 1px solid #F27227;
			border-radius: 5px;
			font-size: 14px;
			line-height: 1.3;
			font-weight: 600;
			text-align: center;
			text-decoration: none;
			cursor: pointer;
			&:hover {
				color: #F27227;
			}
		}
	}

}